import React from 'react'
import { createStackNavigator } from '@react-navigation/stack'
import { createDrawerNavigator } from '@react-navigation/drawer';
import { IndexAgrisysContainer, IndexExampleContainer } from '../Containers'
import { SafeAreaView, StatusBar } from 'react-native'
import { useTheme } from '../Theme'

const MainStack = createStackNavigator();

// @refresh reset
const MainNavigator = () => {
  const { Layout, darkMode, NavigationTheme } = useTheme()
  const { colors } = NavigationTheme
  
  return (
    <SafeAreaView style={[Layout.fill, { backgroundColor: colors.card }]}>
      <MainStack.Navigator headerMode={'none'} initialRouteName={"Home"} mode={"modal"}
        screenOptions={{
          title: 'AgrisysApp',
          headerTintColor: 'white',
          headerTitleAlign: 'center',
      }}>
        <MainStack.Screen name="Home" component={IndexAgrisysContainer} 
          options={({ navigation }) => ({
            headerShown: false,
          })}
        />
        
        {/* not in use.. */}
        <MainStack.Screen name="Test" component={IndexExampleContainer} />
      </MainStack.Navigator>
    </SafeAreaView>
  )
}

export default MainNavigator
