import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update, updateData, create} from '../../Services/Firebase/Sections'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/sections', fetch),
  //update: buildAsyncActions('firebase/sections', update),
  //create: buildAsyncActions('firebase/sections', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'sections',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}