import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update, updateData, create} from '../../Services/Firebase/Graphs'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/graphs', fetch),
  update: buildAsyncActions('firebase/graphs', update),
  //create: buildAsyncActions('firebase/graphs', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'graphs',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}