import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update, updateData, create, _delete} from '../../Services/Firebase/Weighings'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/weighings', fetch),
  //update: buildAsyncActions('firebase/weighings', update),
  create: buildAsyncActions('firebase/weighings', create),
  update: buildAsyncActions('firebase/weighings', update),
  _delete: buildAsyncActions('firebase/weighings', _delete),
  
  reducers: buildAsyncReducers({
    itemKey: 'weighings',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}