import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update, create, _delete } from '../../Services/Firebase/Pens'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/pens', fetch),
  update: buildAsyncActions('firebase/pens', update),
  create: buildAsyncActions('firebase/pens', create),
  _delete: buildAsyncActions('firebase/pens', _delete),

  reducers: buildAsyncReducers({
    itemKey: 'pens',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}