import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { View, Picker } from 'react-native'
import { useTheme } from '../Theme'

const monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];

const Datetime_Picker = ({ Selected, onValueChange }) => {
  const { Layout, Images } = useTheme()

  var year, month, day;
  year = Selected.getFullYear()
  month = Selected.getMonth()
  day = Selected.getDate()

  var Today = new Date();
  var _year = Today.getFullYear()
  var YearItems = []
  for (let index = _year + 25; index > _year - 25; index--) {
    var val = String(index)
    YearItems.push(<Picker.Item key={"Year_" + index} value={index} label={val} />)
  }

  var MonthItems = []
  var _month = Today.getMonth()
  for (let index = 0; index < monthNames.length; index++) {
    var val = String(index)
    MonthItems.push(<Picker.Item key={"Month_" + index} value={index} label={monthNames[index]} />)
  }

  var DayItems = []
  var _days = new Date(_year, _month + 1, 0).getDate()
  for (let index = 1; index <= _days+1; index++) {
    var val = String(index)
    DayItems.push(<Picker.Item key={"Day_" + index} value={index} label={val} />)
  }

  return (
    <View style={[Layout.row, {marginBottom:-10,}]}>
      <Picker selectedValue={year} style={[{minWidth: 101, marginRight:5}]}
        onValueChange={(_year) => {
          year = _year
          onValueChange(new Date(_year, month, day))
        }}>
        {YearItems}
      </Picker>

      <Picker selectedValue={month} style={[{minWidth: 110, marginRight:5}]}
        onValueChange={(_month) => {
          month = _month
          onValueChange(new Date(year, _month, day))
        }}>
        {MonthItems}
      </Picker>

      <Picker selectedValue={day} style={[{minWidth: 83}]}
        onValueChange={(_day) => {
          _days = parseInt(_day)
          onValueChange(new Date(year, month, _days))
        }}>
        {DayItems}
      </Picker>
    </View>
  )
}

Datetime_Picker.propTypes = {
  Selected: PropTypes.instanceOf(Date),
  year: PropTypes.number,
  month: PropTypes.number,
  day: PropTypes.number,
  onValueChange: PropTypes.func
}

Datetime_Picker.defaultProps = {
  Selected: new Date(),
  onValueChange: ()=>{}
}

export default Datetime_Picker
