import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  ScrollView,
  FlatList
} from 'react-native'
import { LoadingSplash, Brand, Frame, Button, ContentProp, CurveViewer, OperationsPanel } from '../../../Components'
import { SectionGrid } from 'react-native-super-grid';

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { ErrorHandler, SetData, WeightCalculate } from '../../../Store/Firebase/Utils'

import FirebaseWeighing from '../../../Store/Firebase/Weighing'
import FirebaseComment from '../../../Store/Firebase/Comment'
import FirebaseBatch from '../../../Store/Firebase/Batch'
import FirebaseGraph from '../../../Store/Firebase/Graph'

import { DataRefs, DataStates, Operations } from '../../../Services/utils/defines'
import { ObjectsEqual, sortObject } from '../../../Services/utils/formators'

const Pen_Display = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const activeData = useSelector(state => state.firebase.activeData?state.firebase.activeData:undefined)
  const batchDatas = useSelector(state => activeData&&activeData.batch&&state.firebase.batchs&&state.firebase.batchs[activeData.batch]?state.firebase.batchs[activeData.batch]:undefined)
  const graphDatas = useSelector(state => batchDatas&&batchDatas.gcurve&&state.firebase.graphs&&state.firebase.graphs[batchDatas.gcurve]?state.firebase.graphs[batchDatas.gcurve]:undefined)

  const penDatas = useSelector(state => activeData&&activeData.pen&&state.firebase.pens&&state.firebase.pens[activeData.pen]?state.firebase.pens[activeData.pen]:undefined)
  const weighingsDatas = useSelector(state => activeData&&activeData.pen&&state.firebase.weighings&&state.firebase.weighings[activeData.pen]?state.firebase.weighings[activeData.pen]:undefined)
  const commentsDatas = useSelector(state => activeData&&activeData.pen&&state.firebase.comments&&state.firebase.comments[activeData.pen]?state.firebase.comments[activeData.pen]:undefined)
  
  //const weightCalcs = useSelector(state => activeData&&state.firebase.activeData&&state.firebase.activeData.weightCalc?state.firebase.activeData.weightCalc:undefined)
  const autoPigs = useSelector(state => state.firebase.autopigs)

  const [last_ActiveData, setlast_ActiveData] = useState(null)
  const [weightCalcs, setweightCalcs] = useState({})

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (activeData && activeData.farm && activeData.pen) {
      dispatch(FirebaseWeighing.action({FarmId: activeData.farm, PenId: activeData.pen, SectionId: activeData.section}))
      dispatch(FirebaseComment.action({FarmId: activeData.farm, PenId: activeData.pen}))
      dispatch(FirebaseBatch.action({FarmId: activeData.farm, BatchId: activeData.batch})).then(data => {
        if (data.meta.requestStatus === "fulfilled"){ // secound hand load Gruve from batch data
          var Datas = data.payload
          if (Datas && Datas.gcurve) {
            dispatch(FirebaseGraph.action({FarmId: activeData.farm, GraphId: Datas.gcurve}))
          }
        }
      })
    }
    /*if (activeData && activeData.farm && activeData.pen && (!autoPigs || typeof autoPigs === 'undefined')) {
      //
    }*/
  },[activeData])
  
  useEffect(() => {
    if (weighingsDatas && graphDatas && !ObjectsEqual(weighingsDatas, last_ActiveData)) {
      setlast_ActiveData(weighingsDatas)
      dispatch(WeightCalculate.action({Weighings: weighingsDatas, Batch:batchDatas, Graphs:graphDatas})).then(data => {
        if (data.meta.requestStatus === "fulfilled") {
          setweightCalcs(data.payload)
        }
      })
    }
  },[weighingsDatas, graphDatas])

  if (activeData && penDatas && typeof penDatas !== 'undefined') {
    var count = "0"
    var batch = "-"
    var age = "-"
    var curve = "-"
    var aweight = "0"
    var agrowth = "0"
    var bmin = undefined
    var bmax = undefined

    if (penDatas.datas){
      if (penDatas.datas.count)count = penDatas.datas.count + ''
      if (penDatas.datas.batch)batch = penDatas.datas.batch
      if (batchDatas) {
        age = Math.floor(((Date.now()-batchDatas.time)/(1000*60*60*24))) + '';
        curve = batchDatas.gcurve;
        bmin = batchDatas.datas.devianceMax +''
        bmax = batchDatas.datas.devianceMax +''
      }
      if (weightCalcs) {
        if (weightCalcs.PenWeight) aweight = Math.round(weightCalcs.PenWeight * 1000) / 1000 +''
        if (weightCalcs.PenGrowth) agrowth = Math.round(weightCalcs.PenGrowth * 1000) / 1000 +''
      }
    }

    return (
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Common.backgroundPrimary]}>
        <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
          <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
            <Button Texts={[activeData.pen]} Title={true}/>
            <View style={{height:20}}/>
            <ContentProp Columns={2} Datas={[[t('Pen_Display.Prop1'), count], [t('Pen_Display.Prop2'), batch], [t('Pen_Display.Prop3'), age], [t('Pen_Display.Prop4'), aweight],
                                             [t('Pen_Display.Prop5'), agrowth], [t('Pen_Display.Prop6'), curve], [t('Pen_Display.Prop7'), t('Pen_Display.Prop7Data', {min:bmin,max:bmax})], 
                                             [t('Pen_Display.Prop8'), t('Pen_Display.Prop8Time',{date:undefined})], [t('Pen_Display.Prop9'), ""]]} />
            <View style={{height:20}}/>
            
            <View style={[Layout.fullWidth, Layout.alignItemsCenter, Common.border, isApp()?{width:'80%'}:{}, {minHeight:400, minWidth:400, marginBottom:20}]}>
              <CurveViewer WeighningsDatas={{Batch:batchDatas, TempCurve:graphDatas, Weights:{[activeData.pen]:weighingsDatas}}} maxHeight={650} />
            </View>

            <View style={[Layout.fullWidth, Layout.colVCenter, {marginBottom:20}]}>
              <ContentProp Columns={1} Datas={[[t('Pen_Display.ControlWeights_Title')]]} />
              <View style={{height:10}} />
              <FlatList style={[Layout.fullWidth, {maxWidth:600, minHeight:125, maxHeight:250}]}
                data={weighingsDatas?Object.keys(weighingsDatas).filter(e => !weighingsDatas[e].deleted && !weighingsDatas[e].edited).reverse():null}
                renderItem={({item, index}) => {
                  const WeightData = weighingsDatas[item]
                  var weight = Math.round(WeightData.weight * 1000) / 1000 +''
                  return(
                    <View style={[Layout.fullSize, Layout.colVCenter]}>
                      <ContentProp Columns={2} Datas={[[t('Pen_Display.WeightTime',{date:new Date(WeightData.time)}), t('Pen_Display.WeightData', {weight: weight, count: WeightData.count})]]} />
                    </View>
                  )}
                }
                  //Setting the number of column
                  numColumns={1}
                  keyExtractor={(item, index) => String(index)}
                />
            </View>

            <View style={[Layout.fullWidth, Layout.colVCenter, {marginBottom:20}]}>
              <ContentProp Columns={1} Datas={[[t('Pen_Display.ActivityLog_Title')]]} />
              <View style={{height:10}} />
              <FlatList style={[Layout.fullWidth, {maxWidth:600, minHeight:125, maxHeight:250}]}
                data={commentsDatas?Object.keys(commentsDatas).reverse():null}
                renderItem={({item, index}) => {
                  const CommentData = commentsDatas[item]
                  var weight = CommentData.weight?Math.round(CommentData.weight * 1000) / 1000 +'':'-'
                  var DataString = ""
                  if (CommentData.action === Operations.NOTE) DataString = t('Pen_Display.NoteData', {note:CommentData.data})
                  else DataString = t('Pen_Display.ActivityData', {count: CommentData.quantity, action: CommentData.action, weight:weight, note:CommentData.data})
                  return(
                    <View style={[Layout.fullSize, Layout.colVCenter]}>
                      <ContentProp Columns={2} Datas={[[t('Pen_Display.ActivityTime',{date:new Date(CommentData.time)}), DataString]]} />
                    </View>
                  )}
                }
                  //Setting the number of column
                  numColumns={1}
                  keyExtractor={(item, index) => String(index)}
                />
            </View>

            <OperationsPanel Type={3}/>
          </View>
        </ScrollView>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Pen_Display
