import React from 'react'
import PropTypes from 'prop-types'
import { View, Image } from 'react-native'
import { useTheme } from '../Theme'

const Frame = ({ logo, protein, bottom, childs }) => {
  const { Layout, Images } = useTheme()

  return (
    <View style={[Layout.fill, Layout.fullSize, {position:'absolute'}]}>
      {logo     &&  <Image style={[Layout.fill, {position:'absolute', top:"5%", left:"2%", width:"33%", height:"15%"}]} source={Images.Logo} resizeMode={'center'}/>}
      {protein  &&  <Image style={[Layout.fill, {position:'absolute', top:"10%", right:"0%", width:"30%", height:"50%"}]} source={Images.Login_protein} resizeMode={'contain'}/>}
      {bottom   &&  <Image style={[Layout.fill, {position:'absolute', bottom:0, width:"100%", height:"40%"}]} source={Images.Login_bottom} resizeMode={'cover'}/>}
    </View>
  )
}

Frame.propTypes = {
  logo: PropTypes.bool,
  childs: PropTypes.node,
}

Frame.defaultProps = {
  logo:true,
  protein: true,
  bottom: true,
  childs:null
}

export default Frame
