import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
} from 'react-native'
import { LoadingSplash, Brand, Frame, Button } from '../../../Components'
import { SectionGrid } from 'react-native-super-grid';

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { EditData, ErrorHandler, SetData } from '../../../Store/Firebase/Utils'
import FirebaseSection from '../../../Store/Firebase/Section'
import FirebasePen from '../../../Store/Firebase/Pen'
import FirebaseWeighing from '../../../Store/Firebase/Weighing'
import FirebaseBatch from '../../../Store/Firebase/Batch'
import FirebaseGraph from '../../../Store/Firebase/Graph'

import { WeightCalc } from '../../../Services/Firebase/Utils'

import { DataRefs } from '../../../Services/utils/defines'
import { ObjectsEqual, sortObject } from '../../../Services/utils/formators'

const Pens = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const farmId = useSelector(state => state.firebase.activeData&&state.firebase.activeData.farm?state.firebase.activeData.farm:undefined)
  const sectionDatas = useSelector(state => state.firebase.sections)
  const pensDatas = useSelector(state => state.firebase.pens)

  const batchDatas = useSelector(state => state.firebase.batchs)
  const graphDatas = useSelector(state => state.firebase.graphs)
  const weighingsDatas = useSelector(state => state.firebase.weighings)
  const autoPigs = useSelector(state => state.firebase.autopigs)

  const [PenData, setPenData] = useState([])

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
    return () => {
      // clean up
    };
  }, [])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (farmId) {
      dispatch(FirebaseWeighing.action({FarmId: farmId, PenId: null, SectionId: null, DataTime:weighingsDatas&&weighingsDatas.update_time?weighingsDatas.update_time:null}))
      dispatch(FirebasePen.action({FarmId: farmId}))
      dispatch(FirebaseSection.action({FarmId: farmId}))
      dispatch(FirebaseBatch.action({FarmId: farmId}))
      dispatch(FirebaseGraph.action({FarmId: farmId}))
    }
    
    if (farmId && (!autoPigs || typeof autoPigs === 'undefined')) {
      
    }
    
    return () => {
      
    }
  },[farmId])

  useEffect(async () => {
    var isCancelled = false;
    if(pensDatas) {
      //await Promise.all(Object.entries(pensDatas).map(async ([Id,Pen])=>{
      Object.entries(pensDatas).map(async ([Id,Pen])=>{
        if (Id === "update_time") return;
        //var color = Colors.gray
        //var displayWeight = null

        if (!isCancelled && Pen.datas && Pen.datas.batch && batchDatas && batchDatas[Pen.datas.batch]) {
          var batch = batchDatas[Pen.datas.batch]
          if (batch && batch.gcurve && graphDatas && graphDatas[batch.gcurve]) {
            var Curve = graphDatas[batch.gcurve]
            if (!isCancelled && Curve && weighingsDatas && weighingsDatas[Id]) {
              //could be await ...
              WeightCalc({ Weighings:weighingsDatas[Id], Batch:batch, Graphs:Curve}).then( (data) => {
                var color = Colors.gray
                var displayWeight = data.PenWeight
                displayWeight = (Math.round(displayWeight * 100) / 100)

                var TargetWeight = data.Batch.datas.target?data.Batch.datas.target:null
                if (TargetWeight && displayWeight >= TargetWeight) {
                  color = Colors.pigpink
                } else if (data.isVirtual) {
                  var devianceMin = data.Batch.datas.devianceMin?data.Batch.datas.devianceMin:3
                  var devianceMax = data.Batch.datas.devianceMax?data.Batch.datas.devianceMax:3
                  var Weight = data.isVirtual.Now
                  var ExValue = data.isVirtual.TempEx
  
                  var devianceValMin = (ExValue/100)* devianceMin
                  var devianceValMax = (ExValue/100)* devianceMax
                  
                  if (ExValue && Weight < ExValue && Weight < ExValue - devianceValMin) {
                    color = Colors.error
                  } else if (ExValue && Weight > ExValue && Weight > ExValue + devianceValMax) {
                    color = Colors.overest
                  } else {
                    color = Colors.success
                  }
                }
                if (!isCancelled) {
                  setPenData(data => {
                    var NewData = {}
                    if (data[Id] && data[Id].AutoPig) NewData[Id]= {datas: Pen.datas, color: color, displayWeight: displayWeight, AutoPig:data[Id].AutoPig}
                    else NewData[Id] = {datas:Pen.datas, color: color, displayWeight:displayWeight}
                    if (NewData[Id] && (!data[Id] || !ObjectsEqual(NewData[Id] && data[Id]))){ 
                      var State = {
                        ...data,
                        ...NewData
                      }
                      return State
                    }
                  })
                }
              })
            }
          }
        }
      })
      //)
    }
    return () => {
      // clean up
      isCancelled = true
    };
  },[pensDatas, batchDatas, graphDatas, autoPigs, weighingsDatas])

  useEffect(async () => {
    if(pensDatas) {
     await Promise.all(Object.entries(pensDatas).map(async ([Id,Pen])=>{
        var AutoPig = false;
        if (autoPigs && autoPigs.some(e => e[item.item])) {
          //console.log("Pens: " + item.item + " is autopig property")
          //If data check batch state.. else color bumblebee
        }
        if (Pen.datas && Pen.datas.autopig) AutoPig = Pen.datas.autopig // replace with above check.. 
        
        setPenData(data => {
          if (data[Id]) data[Id].AutoPig = AutoPig;
          else data[Id] = {AutoPig: AutoPig}
          return data
        })
      }))
    }
  },[pensDatas, autoPigs])

  if (sectionDatas && typeof sectionDatas !== 'undefined') {
    return (
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Layout.colVCenter, Common.backgroundPrimary]}>
        <SectionGrid style={[Layout.fill, Layout.fullSize, Gutters.smallVPadding, Gutters.smallHPadding]}
          itemDimension={75}
          itemContainerStyle={[{width:75,height:75}]}
          spacing={2}
          sections={
            Object.keys(sectionDatas).filter(e => e !== 'update_time').map((Section) => {
              var Pens = sortObject(sectionDatas[Section].pens)
              return({
                title: Section,
                data: Object.keys(Pens)
              })
            })
          }

          renderSectionHeader={({ section }) => (
            <View style={[Layout.midWidth, {marginBottom:0}, isApp()?{width:"65%"}:{width:"100%"}, ]}>
              <Button Texts={[section.title]} Title={true} />
            </View>
          )}

          renderItem={(item) => {
            var color = Colors.gray
            var displayWeight = null
            var AutoPig = false;

            if (PenData[item.item]){
              var Pen = PenData[item.item]
              if (Pen.color) color = Pen.color
              if (Pen.displayWeight) displayWeight = Pen.displayWeight
              if (Pen.AutoPig) AutoPig = Pen.AutoPig
            }

            return(
              <TouchableOpacity style={[Common.button.outlineRounded, {borderColor:'#000000', borderWidth:AutoPig?5:0, width:70, height:70, alignItems:"center", justifyContent:"center", backgroundColor:color}]}
                onPress={() => {
                  //pen = item.item  || item.section.originalData[item.index]
                  //section = item.section.title 
                  var batch = Pen && Pen.datas && Pen.datas.batch?Pen.datas.batch:null
                  dispatch(EditData.action({DataRef:DataRefs.ACTIVE, Data:{farm:farmId, section:item.section.title, pen:item.item, batch:batch}}))
                  navigationRef.current?.navigate('User', {screen: 'Pen_Display'})
                }}>
                <Text>{item.item}</Text>
                {displayWeight?(<Text>{displayWeight + " kg"}</Text>):(<></>)}
              </TouchableOpacity>
            )
          }}

          />
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Pens
