import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  ScrollView,
  Linking
} from 'react-native'
import { LoadingSplash, Brand, Frame, Button, ContentProp } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { ErrorHandler } from '../../../Store/Firebase/Utils'
import FirebaseFarm from '../../../Store/Firebase/Farm'

const DashBoard = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)
  /*const tests = useSelector(state => state.firebase)
  console.log(tests)*/
  const farmDatas = useSelector(state => state.firebase.farms&&state.firebase.activeData&&state.firebase.activeData.farm?
                                state.firebase.farms[state.firebase.activeData.farm]:undefined)

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  /*const test = useSelector(state => state.firebase)
  console.log(test)*/
  if (farmDatas && typeof farmDatas !== 'undefined'){
    return(
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Common.backgroundPrimary]}>
        <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
          <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
            <Button Texts={[t('DashBoard.Title')]} Title={true}/>
            <View style={{height:20}}/>
            <ContentProp Columns={2} Datas={[[t('DashBoard.Prop1'), farmDatas.name],[t('DashBoard.Prop2'), farmDatas.manager],[t('DashBoard.Prop3'), farmDatas.email]]}/>
            <View style={{height:20}}/>
            <Text>Element not done</Text>
            <Text>Element not done</Text>
            <View style={{height:20}}/>
            <Text>{t('DashBoard.BatchesTitle')}</Text>
            <Text>Element not done</Text>
            <Text>Element not done</Text>
          </View>
        </ScrollView>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default DashBoard
