import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {getUserData, signin, logout, resetPassword, fetch, update} from '../../Services/Firebase/User'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/user', getUserData),
  signin: buildAsyncActions('firebase/user', signin),
  logout: buildAsyncActions('firebase/user', logout),
  reset: buildAsyncActions('firebase/user', resetPassword),
  fetch: buildAsyncActions('firebase/user', fetch),
  update: buildAsyncActions('firebase/user', update),
  
  reducers: buildAsyncReducers({
    itemKey: 'user',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}