import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
} from 'react-native'
import { Input } from 'react-native-elements'
import { Brand, Frame, Button } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef } from '../../../Navigators/Root'

import FirebaseUser from '../../../Store/Firebase/User'
import { ErrorHandler } from '../../../Store/Firebase/Utils'

const LoginContainer = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()

  const [Email, setEmail] = useState("")
  const [Pass, setPass] = useState("")

  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)
  
  const signin = (_Email, _Pass) => {
    dispatch(FirebaseUser.signin({Email: _Email, Pass: _Pass}))
  }
  const resetPass = (_Email) => {
    dispatch(FirebaseUser.reset({Email: _Email}))
  }
  const logout = () => {
    dispatch(FirebaseUser.logout())
  }

  return (
    <View style={[Layout.fill, Layout.colCenter, Gutters.smallHPadding, Common.backgroundPrimary]}>
      <Frame />
      {firebaseError && firebaseError.message && (
        <Text style={[{color:Colors.error}]}>{firebaseError.message}</Text>
      )}
      <View style={[Common.border, Layout.midWidth, Layout.colCenter, Gutters.regularBMargin, Gutters.smallVPadding, {backgroundColor:"white", maxWidth:650}] }>
        <View style={[
          Layout.fill, Layout.fullWidth, Gutters.smallVMargin, {maxWidth:600}
        ]}>
          <Input
            label={t('AgrisysConnect.label1')}
            onChangeText={text => 
              setEmail(text)
            }
            editable={!firebaseLoading}
            keyboardType={'default'}
            value={Email}
            selectTextOnFocus
            style={[Layout.fill, Common.textInput, {width:100}]}
          />
          <Input
            label={t('AgrisysConnect.label2')}
            onChangeText={text => 
              setPass(text)
            }
            editable={!firebaseLoading}
            keyboardType={'default'}
            value={Pass}
            selectTextOnFocus
            secureTextEntry={true}
            style={[Layout.fill, Common.textInput, {width:100}]}
          />
        </View>
        <Button Texts={[t('AgrisysConnect.submit_btn')]} Icons={[Images.User]}
          Actions={
            [ele => {
              if (Email && Email !== "") {
                if (Email === "CLEAR") { this.props.clearDataMem() }
                else if (Pass && Pass !== "") {
                  signin(Email, Pass)
                }
              }else {
                dispatch(ErrorHandler.action({message: "Please provide an Email"}))
              }
            }]
          }
        />
      </View>

      <View style={[Layout.halvWidth, {maxWidth:450}]}>
        <Button Texts={[t('AgrisysConnect.reset'), t('AgrisysConnect.create')]} Icons={[]} Actions={[ele => {
          switch(ele.index) {
            case 0:
              if (Email && Email !== "") {
                resetPass(Email)
              }else {
                dispatch(ErrorHandler.action({message: "Please provide an Email"}))
              }
              break;
            case 1:
              navigationRef.current?.navigate('Auth', {screen: 'CreateUser'})
              break;
          }
        }]} />
      </View>
    </View>
  )
}

export default LoginContainer
