import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

async function fetch({FarmId}) {
  var ref = "farms/"
  if (FarmId) ref += FarmId
  var farmData = await database().ref(ref).once('value').then((_farmData) => {
    var data = _farmData.val()
    if (FarmId) {
      data = {[FarmId]:data}
    }
    return data
  })
  return farmData
}

async function update({FarmId, Data}) {
  var res
  if (FarmId){
    res = await database().ref('farms/'+ FarmId).update({
      ...Data
    }).then(async ()=> {
      var data = await fetch(FarmId)
      return data
    }).catch(error => {
      console.log("Update Farm Error: ", error);
      return handleError({ message: 'Update Farm Error' })
    })
  } else {
    const farm = database().ref('farms/').push()
    const farmid = farm.key
    res = await farm.set({
      Data
    }).then(async () => {
      var data = await fetch(FarmId)
      return data
    }).catch(error => {
      console.log("Update Farm Error: ", error);
      return handleError({ message: 'Update Farm Error' })
    })
  }
  return res
}

async function updateData({FarmId, Data}) {
  if (FarmId) {
    const ref = "farms_data/" + FarmId;
    var res = await database().ref(ref).update(
      Data
    ).then(() => {
      return null //not good... null
    }).catch(error => {
      if (error.code === 'database/permission-denied') {
        console.log("Farm data update was permission denied.");
      }else if (error.code) {
        console.log("User update Error: ", error);
      }
      return handleError({ message: 'Farm updateData Error' })
    })
    return res
  } else {
    console.log("Farm uid in updateData unknown");
    return handleError({ message: 'Farm uid in updateData unknown' })
  }
}

async function create({Data}){
  const user = auth().currentUser;
  const ref = "users/" + user.uid;

  const farm = database().ref('farms/').push()
  const farmid = farm.key
  
  var res = await farm.set({
    email: user.email,
    manager: user.displayName ? user.displayName : user.email,
    name: Data.name
  }).then(async () => {
    var data = await fetch(FarmId).then((_data) => {
      return _data
    })
    return data
  }).catch(error => {
    console.log("Create Farm Error: ", error);
    return handleError({ message: 'Create Farm Error' })
  })
  return res
}

export {
  fetch, 
  update,
  updateData,
  create
}