import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

async function fetch({FarmId, GraphId, DataTime}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    if (_rend.getState().firebase.graphs) {
      var time = await database().ref(ref + '/update_time').once('value').then((_timeData) => {
        if (_rend.getState().firebase.graphs.update_time < _timeData.val()) return false
        else return true
      })
      if (time) {
        var StateGraphs = _rend.getState().firebase.graphs
        if (!GraphId && !StateGraphs[GraphId]) {
          return StateGraphs
        }
      }
    }

    if (GraphId) ref += "/" + GraphId;
    var res = await database().ref(ref).once('value').then(async (graph) => {
      var Graphs = null
      var GraphVal = graph.val()
      if (GraphVal) {
        if (GraphId) {
          var newGraph = {[graph.key]: GraphVal}
          Graphs = {...Graphs, ...newGraph}
        }else {
          Graphs = {...Graphs, ...GraphVal}
        }
      }else {
        GraphId = null
      }
      if (!GraphId) {
        Graphs = await database().ref("farms_data/Agrisys/graph").once('value').then((Agraph) => {
          var AgraphVal = Agraph.val();
          if (AgraphVal) {
            Graphs = {...AgraphVal, ...Graphs}
          }
          return Graphs
        }).catch(error => {
          console.log("Get Agrisys graph Error: ", error);
          if (Graphs) return Graphs
          else return handleError({ message: 'Get Agrisys graph Error' })
        })
        
      }
      return Graphs
    }).catch(error => {
      console.log("Get farm graph Error: ", error);
      return handleError({ message: 'Get farm graph Error' })
    })
    return res
  }
  return handleError({ message: 'Fecth Graphs Error' })
}

async function update({FarmId, GraphId, Graph}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    if (GraphId) {
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      ref += "/" + GraphId + "/"
      var res = await database().ref(ref).update({
        ...Graph
      }).then(async () => {
        var res2 = await fetch({FarmId: FarmId}).then((_Graph)=>{
          return _Graph
        })
        return res2
      }).catch(error => {
        console.log("Update Graph Error: ", error);
        return handleError({ message: 'Update Graphs Error' })
      })
      return res
    }
  }
  return handleError({ message: 'Update Graphs Error' })
}

export {
  fetch, 
  update,
}