import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

async function fetch({FarmId, PenId}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (_rend.getState().firebase.pens) {
      var time = await database().ref(ref + '/update_time').once('value').then((_timeData) => {
        if (_rend.getState().firebase.pens.update_time < _timeData.val()) return false
        else return true
      })
      if (time) {
        var StatePens = _rend.getState().firebase.pens
        if (!PenId && !StatePens[PenId]) {
          return StatePens
        }
      }
    }

    if (PenId){
      ref += "/" + PenId
    }
    
    var res = await database().ref(ref).once('value').then((_pensData) => {
      if (PenId) {
        var penData = _pensData.val()
        if (!penData) penData = {}
        if (!penData.datas) penData.datas = {}

        var res = {[PenId]: penData}
        var state = _rend.getState()
        if (state.firebase.pens){
          res = {...state.firebase.pens}
          res[PenId] = penData
        }
        return res;
      } else {
        var res = _pensData.val()
        return res
      }
    })
    return res
  }
  return handleError({ message: 'Fecth Pens Error' })
}

async function update({FarmId, PenId, Pen}, _rend){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (PenId) {
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })
      var res = await fetch({FarmId: FarmId, PenId: PenId}, _rend).then(async (_Pen) => {
        ref += "/" + PenId + "/"
        var data = Pen.datas
        var _data = _Pen[PenId].datas
        var Datas = {..._data, ...data}
    
        _Pen[PenId].datas = Datas
        var res2 = await database().ref(ref).update({
          ..._Pen[PenId],
        }).then(async () => {
          var penData = _Pen[PenId]
          return {[PenId]: penData}
        }).catch(error => {
          console.log("Update Pen Error: ", error);
          return handleError({ message: 'Update Pens Error' })
        });
        return res2
      })
      return res
    }
  }
  return handleError({ message: 'Update Pens Error' })
} 

async function create({FarmId, PenId, SectionId}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    if (PenId) {
      var ti = await database().ref(ref + "pens").update({
        update_time: database.ServerValue.TIMESTAMP
      })

      var res = await database().ref(ref + "pens/" + PenId + "/").set({
        section: SectionId,
      }).then(async () => {
        var res2 = await database().ref(ref + "sections/" + SectionId + "/pens").update({
          [PenId]: true,
        }).then(async () => {
          var ti2 = await database().ref(ref + "sections/").update({
            update_time: database.ServerValue.TIMESTAMP,
          })

          var res3 = await fetch({FarmId: FarmId, PenId: PenId}, _rend).then((_Pen) => {
            return _Pen
          }).catch(error => {
            console.log("Update Pen Error: ", error);
            return handleError({ message: 'Update Pens Error' })
          });;
          return res3
        });
        return res2
      }).catch(error => {
        console.log("Update Pen Error: ", error);
        return handleError({ message: 'Update Pens Error' })
      });
      return res
    }
  }
  return handleError({ message: 'Update Pens Error' })
}

async function _delete({FarmId, PenId, SectionId}, _rend) {
  if (FarmId) {
    var re = "farms_data/" + FarmId + "/"
    var ref = re + "pens";
    console.log(PenId, SectionId)
    if (PenId) {
      await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      var res = await database().ref(ref + "/" + PenId + "/").remove().then(async () => {
        var res2
        if (SectionId && SectionId !== "") {
          await database().ref(re + "sections/" + SectionId + "/pens/" + PenId).remove().then(async () => {
            await database().ref(re + "sections/").update({
              update_time: database.ServerValue.TIMESTAMP,
            })
          })
        } 
        res2 = await fetch({FarmId: FarmId}, _rend).then((_Pen) => {
          return _Pen
        });
        return res2
      })
      return res
    }
  }
  return handleError({ message: 'Update Pens Error' })
}

export {
  fetch,
  update,
  create,
  _delete
}