import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  ActivityIndicator,
  Text,
  TouchableOpacity,
} from 'react-native'
import { Input } from 'react-native-elements'
import { Brand, Frame, Button } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef } from '../../../Navigators/Root'

import FirebaseUser from '../../../Store/Firebase/User'

const LoginContainer = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images } = useTheme()

  const [Email, setEmail] = useState("")
  const [Pass, setPass] = useState("")

  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const signin = (_Email, _Pass) => {
    dispatch(FirebaseUser.signin({Email: _Email, Pass: _Pass}))
  }
  const resetPass = (_Email) => {
    dispatch(FirebaseUser.reset({Email: _Email}))
  }
  const logout = () => {
    dispatch(FirebaseUser.logout())
  }

  return (
    <View style={[Layout.fill, Layout.colCenter, Gutters.smallHPadding, Common.backgroundPrimary]}>
      <Frame />
      {firebaseError && firebaseError.message && (
        <Text>{firebaseError.message}</Text>
      )}
      <View style={[Common.border, Layout.midWidth, Layout.colCenter, Gutters.regularBMargin, Gutters.smallVPadding, {backgroundColor:"white", maxWidth:650}] }>
        <View style={[
          Layout.fill, Layout.fullWidth, Gutters.smallVMargin, {maxWidth:600}
        ]}>
          <Input style={[Common.textInput, {height:40, width:160}]}
            containerStyle={[Layout.quarterWidth,{width:180, height:70, borderWidth:1, marginTop:10}]}
            inputStyle={{paddingHorizontal:5}}
            label={"Farm-tag:"}
            onChangeText={text => 
              setFarmSelect(text)
            }
            editable={false}
            keyboardType={'default'}
            value={FarmSelect.name}
            selectTextOnFocus
          />
          <Input
            label="Email"
            onChangeText={text => 
              setEmail(text)
            }
            editable={!firebaseLoading}
            keyboardType={'default'}
            value={Email}
            selectTextOnFocus
            style={[Layout.fill, Common.textInput]}
          />
          <Input
            label="Password"
            onChangeText={text => 
              setPass(text)
            }
            editable={!firebaseLoading}
            keyboardType={'default'}
            value={Pass}
            selectTextOnFocus
            secureTextEntry={true}
            style={[Layout.fill, Common.textInput]}
          />
        </View>
        <Button Texts={["Login"]} Icons={[Images.User]}
          Actions={
            [ele => {
              if (Email && Email !== "") {
                if (Email === "CLEAR") { this.props.clearDataMem() }
                else if (Pass && Pass !== "") {
                  signin(Email, Pass)
                }
              }
            }]
          }
        />
      </View>
    </View>
  )
}

export default LoginContainer
