import React from 'react'
import PropTypes from 'prop-types'
import { View, TouchableOpacity, Text } from 'react-native'
import { ListItem } from 'react-native-elements'
import { useTheme } from '../Theme'

const CustomListItem = ({key, Title, Subtitle, onPress, itemStyle }) => {
  const { Layout, Common, Images } = useTheme()

  return (
    <ListItem key={key} containerStyle={[Layout.fill, Common.border, {flexDirection:"column", marginBottom:0, marginTop:0, padding:0, borderRadius:0}, itemStyle]} button onPress={onPress}>
      <ListItem.Content>
        <ListItem.Title style={{height:30, alignContent:'center'}}>{Title}</ListItem.Title>
        {Subtitle&&<ListItem.Subtitle>{Subtitle}</ListItem.Subtitle>}
      </ListItem.Content>
    </ListItem>
  )
}

CustomListItem.propTypes = {
  key: PropTypes.number,
  Title: PropTypes.string,
  Subtitle: PropTypes.string,
  onPress: PropTypes.func,
  itemStyle: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.array
  ]),
}

CustomListItem.defaultProps = {
  key: 0,
  Title: "",
  Subtitle: null,
  onPress: null,
  itemStyle: {}
}

export default CustomListItem
