import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  ActivityIndicator,
  Text,
  TouchableOpacity,
  SafeAreaView,
  ScrollView
} from 'react-native'
import { Brand, Frame, Button } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { DataRefs } from '../../../Services/utils/defines'
import { SetData } from '../../../Store/Firebase/Utils'
import FirebaseUser from '../../../Store/Firebase/User'

const MenuView = (props) => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const User = useSelector(state => state.firebase.user)
  const [Username, setUsername] = useState("")

  const logout = () => {
    dispatch(SetData.action({DataRef:DataRefs.FARMS, Data:null}))
    dispatch(FirebaseUser.logout())
  }

  useEffect(() => {
    if (User.displayName) {
      setUsername(User.displayName)
    } else if (User.name){
      setUsername(User.name)
    }else {
      setUsername("");
    }
  }, [User])

  return(
    <SafeAreaView style={[Layout.fill, Common.backgroundPrimary]}>
      <ScrollView style={Gutters.regularBPadding}>
        <View style={[Layout.fill, Layout.fullSize, Layout.colCenter]}>
          <Text style={[Fonts.textRegular, {paddingVertical:35, alignSelf:'center'}]}>{t('AgrisysMenu.Welcome', {name:Username})}</Text>
          <View style={[Layout.midWidth, {maxWidth:300, alignSelf:'center'}]}>
            <Button Texts={[t('AgrisysMenu.DashBoard'), t('AgrisysMenu.Pens'), t('AgrisysMenu.Batchs'), t('AgrisysMenu.Growth'), t('AgrisysMenu.Operations'), t('AgrisysMenu.Autofunctions'), t('AgrisysMenu.FeedingFunctions'), t('AgrisysMenu.Pigs')]} 
                    Icons={[Images.DashBoard, Images.Pens, Images.Section, Images.GCurves, Images.System, Images.AutoPig, Images.Feed, Images.Sow]} Actions={[ele => {
              var _View = ["DashBoard","Pens","Batchs","Growth","Operations","Autofunctions","FeedingFunctions","Pigs"][ele.index]
              props.setView({View:_View})
            }]} />
            <View style={{minHeight: 30}}/>
            <Button Texts={[t('AgrisysMenu.Logout')]} Icons={[]} Actions={[ele => {
              logout()
              navigationRef.current?.navigate('Auth', {screen: 'Home'})
            }]} />
          </View>
        </View>
      </ScrollView>
    </SafeAreaView>
  )
}

export default MenuView