import React from 'react'
import { View } from 'react-native'
import { Icon } from 'react-native-elements'
import { navigationRef } from '../Navigators/Root'
import { createStackNavigator } from '@react-navigation/stack'
import { createDrawerNavigator } from '@react-navigation/drawer';

import { IndexUserContainer, DashBoard, Pens, Pen_Display, Batchs, GrowthCurves, Operations, Weighing, DataEditor, Autopigs } from '../Containers'
import { SafeAreaView, StatusBar } from 'react-native'
import { useTranslation } from 'react-i18next'
import { useTheme } from '../Theme'

const UserStack = createStackNavigator();

// @refresh reset
const UserNavigator = () => {
  const { t } = useTranslation()
  const { Layout, darkMode, NavigationTheme } = useTheme()
  const { colors } = NavigationTheme
  return (
    <SafeAreaView style={[Layout.fill, { backgroundColor: colors.card }]}>
      <UserStack.Navigator headerMode={'screen'} initialRouteName={"Home"} mode={"modal"}
        screenOptions={{
          title: 'AgrisysApp',
          headerTintColor: 'white',
          headerTitleAlign: 'center',
      }}>
        <UserStack.Screen name="Home" component={IndexUserContainer} 
          options={({ navigation }) => ({
            title: t('AgrisysMenu.NavTitle'),
            headerLeft: (props) => (
              <View style={{padding:6, paddingLeft: 15}}>
                <Icon name="md-arrow-back" type='ionicon' size={25} color="white" iconStyle={{color:"white"}} onPress={() => {
                  if (props.label === "Admin") {
                    navigationRef.current?.navigate('Admin', {screen: 'Home'})
                  }else {
                    navigationRef.current?.navigate('Main', {screen: 'Home'})
                  }}
                }/>
              </View>
            ),
          })}
        />
        <UserStack.Screen name="DashBoard" component={DashBoard} 
          options={({ navigation }) => ({
          })}
        />
        <UserStack.Screen name="Pens" component={Pens} 
          options={({ navigation }) => ({
          })}
        />
        <UserStack.Screen name="Pen_Display" component={Pen_Display} 
          options={({ navigation }) => ({
            title: 'Pen Display',
          })}
        />
        <UserStack.Screen name="Batchs" component={Batchs} 
          options={({ navigation }) => ({
          })}
        />
        <UserStack.Screen name="GrowthCurves" component={GrowthCurves} 
          options={({ navigation }) => ({
          })}
        />
        <UserStack.Screen name="Operations" component={Operations} 
          options={({ navigation }) => ({
          })}
        />
        <UserStack.Screen name="WeighingOperations" component={Weighing} 
          options={({ navigation }) => ({
            title: 'Weighing',
          })}
        />
        <UserStack.Screen name="DataEditor" component={DataEditor} 
          options={({ navigation }) => ({
            title: 'Edit Farm',
          })}
        />
        <UserStack.Screen name="Autopigs" component={Autopigs} 
          options={({ navigation }) => ({
          })}
        />
      </UserStack.Navigator>
    </SafeAreaView>
  )
}

export default UserNavigator
