import React from 'react'
import PropTypes from 'prop-types'
import { View, Image, Text, TouchableOpacity } from 'react-native'
import { useTheme } from '../Theme'

const Button = ({ Texts, Icons, Actions, maxWidth, Frame, Title }) => {
  const { Common, Layout, Gutters, Images, Fonts, Colors } = useTheme()
  
  return (
    <View style={[Layout.fullWidth, Frame?Common.border:{}, Frame?{paddingVertical:10}:{}, { maxWidth: maxWidth }]}>
      {Texts.map((text, index) => {
        var icon = Icons[index]?Icons[index]:null
        var Action = Actions[index]?Actions[index]:Actions[0]
        return (
          <TouchableOpacity key={index} style={[Common.button.filledRounded, index>0?Gutters.smallTMargin:{}, icon?{minHeight:60}:{minHeight:50}, Title?{backgroundColor:Colors.AgriDarkblue, borderColor:Colors.AgriDarkblue}:{}]} 
            onPress={() => {
              if (Action && !Title){
                Action({index:index, text:text})
              }
            }
          }>
            {icon?(
              <View style={[Layout.fill, Layout.rowCenter]}>
                <Text style={[Layout.fill, Fonts.textCenter, Fonts.textSmall, Fonts.textReverse]}>{text}</Text>
                <View style={[Layout.colCenter, Layout.quarterWidth, {height:50}]}>
                  <Image style={Layout.fullSize} source={icon} resizeMode={'contain'} />
                </View>
              </View>
            ):(
              <View style={[Layout.fill, Layout.rowCenter]}>
                <Text style={[Layout.fill, Fonts.textCenter, Fonts.textSmall, Fonts.textReverse]}>{text}</Text>
                {Icons.length > 0 && (
                  <View style={[Layout.colCenter, Layout.quarterWidth, {height:50}]}/>
                )}
              </View>
            )}
          </TouchableOpacity>
        )
      })}
    </View>
  )
}

Button.propTypes = {
  Texts: PropTypes.arrayOf(PropTypes.string),
  Icons: PropTypes.arrayOf(PropTypes.string),
  Actions: PropTypes.arrayOf(PropTypes.func),
  maxWidth: PropTypes.number,
  Frame: PropTypes.bool,
  Title: PropTypes.bool,
}

Button.defaultProps = {
  Texts: [],
  Icons: [],
  Actions: [],
  maxWidth: 600,
  Frame: false,
  Title: false
}

export default Button
