import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  ActivityIndicator,
  Text,
  TouchableOpacity,
  Image,
  ScrollView,
} from 'react-native'
import { Brand, Frame, Button } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import MenuView from './MenuView'
import SystemBoard from './SystemBoard'

import { ErrorHandler } from '../../../Store/Firebase/Utils'

const IndexAdminContainer = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const [isAppView, setIsAppView] = useState(null)
  const [currentView, setView] = useState(null)

  useEffect(() => {
    setIsAppView(isApp())
  })

  const changeView = ({ View }) => {
    if (isAppView) {
      navigationRef.current?.navigate('Admin', {screen: View})
    }
    else {
      switch(View){
        case "UserBoard":
          break;
        case "System":
          setView([(<SystemBoard key="View_Board"/>)])
          break;
        case "Tools":
          break;
      }
    }
  }

  if (isAppView === null){
    return(<ActivityIndicator />)
  } else if (isAppView) {
    if (currentView) return currentView
    else return (<MenuView setView={changeView} />)
  } else {
    if (!currentView) setView([(<SystemBoard key="View_Board"/>)])
    return(
      <View style={[Layout.fillRow, Layout.fullSize, {alignItems:'flex-start'}, Common.backgroundPrimary]}>
        <View style={[Layout.fillCol, Layout.fullHeight, {minWidth:300, maxWidth:320, borderRightWidth:1}]}>
          {/* Menu */}
          <ScrollView style={[Layout.fill]}>
            <MenuView setView={changeView} />
          </ScrollView>
          <View style={[Layout.fullWidth, {height: 110, marginBottom:15}]}>
            <Image style={[Layout.fullSize]} source={Images.Logo} resizeMode={'contain'} />
          </View>
        </View>
        <View style={[Layout.fillCol, Layout.fullSize, {alignItems:'flex-start' }]}>
          {/* View */}
          {currentView}
        </View>
      </View>
    )
  }
}

export default IndexAdminContainer
