import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

import { fetch as SectionFetch} from '../../Services/Firebase/Sections'

async function fetch({FarmId, PenId, SectionId, limitStart = 0, limitTo = 9999, DataTime, MemSections}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";

    if (_rend.getState().firebase.weighings) {
      var time = await database().ref(ref + '/update_time').once('value').then((_timeData) => {
        if (_rend.getState().firebase.weighings.update_time < _timeData.val()) return false
        else return true
      })
      if (time) {
        var StateWeighings = _rend.getState().firebase.weighings
        if (!PenId && !StateWeighings[PenId]) {
          return StateWeighings
        }
      }
    }

    if (PenId) {
      console.log("Weight fetch called, getting Pen")
      ref += "/" + PenId
      var res = {[PenId]: {}}
      var state = _rend.getState()
      if (state.firebase.weighings){
        res = {...state.firebase.weighings}
      }
      var te = await database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', async snapshot => {
        res[snapshot.key] = {}
        snapshot.forEach(c => {
          res[snapshot.key][c.key] = c.val()
        })
      }).catch(error => {
        console.log(error)
        return handleError({ message: 'Fecth Weights Error' })
      })
      return res
    } else {
      console.log("Weight fetch called, getting Sections")
      var weighings = {}
      var state = _rend.getState()
      if (state.firebase.weighings){
        weighings = {...state.firebase.weighings}
      }
      await SectionFetch({FarmId}, _rend).then(async (sections) => {
        var Sections = sections?sections:MemSections
        if (!_rend.signal.aborted) {
          await Promise.all(Object.entries(Sections).map(async ([section,data]) => {
            if (data && data.pens && !_rend.signal.aborted) {
              await Promise.all(Object.entries(data.pens).map(async ([pen,data]) => {
                if (pen !== "update_time" && !_rend.signal.aborted) {
                  await fetch({FarmId:FarmId, PenId:pen, limitStart:0, limitTo:limitTo}, _rend).then(async (weights) => {
                    if (Object.keys(weights[Object.keys(weights)[0]]).length > 0) {
                      weighings[pen] = {...weights[pen]}
                    }else if (weighings[pen]){
                      delete weighings[pen]
                    }
                  }).catch(error => {
                    console.log(error)
                  })
                }
              }))
            }
          }))
        }
      }).catch(error => {
        console.log(error)
        return handleError({ message: 'Fecth Weights Error' })
      })
      if (Object.keys(weighings).length > 0) {
        weighings.update_time = Date.now()
        return weighings
      }
    }
  }
  return handleError({ message: 'Fecth Weights Error' })
}

async function create({FarmId, PenId, Data},_rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId){
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      ref += "/" + PenId + "/"
      const weightRef = await database().ref(ref).push()
      const weightId = weightRef.key
      var res = await weightRef.set({
        ...Data
      }).then(async () => {
        var res2 = await fetch({FarmId:FarmId, PenId:PenId, DataTime: "SET"}, _rend).then((weights)=>{
          return weights
        })
        return res2
      }).catch(error => {
        console.log("Create Weight Error: ", error);
        return handleError({ message: 'Create Weight Error' })
      })
      return res
    } else {
      //...Weight other than PenId ??
    }
  }
  return handleError({ message: 'Create Weights Error' })
}

async function update({FarmId, PenId, WeightId, Data}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId){
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      ref += "/" + PenId + "/"
      if (WeightId) {
        const weightRef = await database().ref(ref).push()
        const weightId = weightRef.key
        var res = await weightRef.set({
          ...Data
        }).then(async () => {
          ref += "/" + WeightId

          var res2 = await database().ref(ref).update({
            edited: weightId,
          }).then(async () => {
            var res3 = await fetch({FarmId:FarmId, PenId:PenId, DataTime:"SET"}, _rend).then((weights)=>{
              return weights
            })
            return res3
          }).catch(error => {
            console.log("Delete Weight Error: ", error);
            handleError({ message: 'Delete Weights Error'})
          })
          return res2
        }).catch(error => {
          console.log("Update Weight Error: ", error);
          return handleError({ message: 'Update Weight Error' })
        })
        return res
      }
    }
  }
  return handleError({ message: 'Update Weights Error' })
}

async function _delete({FarmId, PenId, WeightId}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId) {
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      ref += "/" + PenId + "/"
      if (WeightId) {
        ref += "/" + WeightId
        var res = await database().ref(ref).update({
          deleted: true,
        }).then(async () => {
          var res2 = await fetch({FarmId:FarmId, PenId:PenId, DataTime:"SET"}, _rend).then((weights)=>{
            return weights
          })
          return res2
        }).catch(error => {
          console.log("Delete Weight Error: ", error);
          handleError({ message: 'Delete Weights Error'})
        })
        return res
      }
    }
  }
  return handleError({ message: 'Delete Weights Error' })
}

export {
  fetch,
  create,
  update,
  _delete,
}