import React from 'react'
import PropTypes from 'prop-types'
import { View, Image, Text, TouchableOpacity } from 'react-native'
import { useTheme } from '../Theme'

const ContentProp = ({ Columns, Datas, Actions, Title, maxWidth, Frame }) => {
  const { Common, Layout, Gutters, Images, Fonts, Colors } = useTheme()
  
  return (
    <View style={[Layout.fullWidth, { maxWidth: maxWidth }]}>
      {/* Title element */}
      <View style={[Layout.fullWidth, Frame?Common.border:{}]}>
        {Datas.map((data, row) => {
          var _Columns = []
          for (let column = 0; column < Columns; column++) {
            var Action = Actions[column]?Actions[column]:Actions[0]
            var data = Datas[row][column]
            _Columns.push(
              <TouchableOpacity style={[Layout.fill, Layout.fullWidth, Layout.colCenter, Common.border]} onLongPress={() => {
                if (Action) {
                  Action({data: data, row:row, column:column})
                }
              }} key={"data_" + row + "_" + column}>
                <Text>{data}</Text>
              </TouchableOpacity>
            )
          }
          return (
            <View key={"row"+row} style={[Layout.fill, Layout.row]}>
              { _Columns }
            </View>
          )
        })}
      </View>
    </View>
  )
}

ContentProp.propTypes = {
  Columns: PropTypes.number,
  Datas: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
  Actions: PropTypes.arrayOf(PropTypes.func),
  Title: PropTypes.string,
  maxWidth: PropTypes.number,
  Frame: PropTypes.bool,
}

ContentProp.defaultProps = {
  Columns: 2,
  Datas: {},
  Actions: [()=>{}],
  Title: null,
  maxWidth: 600,
  Frame: false
}

export default ContentProp
