import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

import { fetch as PenFetch} from './Pens'

async function fetch({FarmId, TagId}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pigs";
    if (TagId){
      ref += "/" + TagId
    }
    var res = await database().ref(ref).once('value').then((_pigsData) => {
      if (TagId) {
        var pigData = _pigsData.val()
        if (!pigData) return null;
        if (!pigData.datas) pigData.datas = {}
        if (!pigData.datas.gcurve) pigData.datas.gcurve = "default";

        return {[TagId]: pigData}
      } else {
        return _pigsData.val()
      }
    })
    return res
  }
  return handleError({ message: 'Fecth Pigs Error' })
}

async function updatePigPen({FarmId, TagId, PenId, Action}) {
  if (FarmId && PenId) {
    var ref = "farms_data/" + FarmId + "/" + "pens/" + PenId + "/datas"
    var res = await database().ref(ref + "/count").once('value').then(async (counts) => {
      var val = counts.val()
      if (!val) val = 0
      if (Action && !isNaN(Action)) {
        val = parseInt(val) + parseInt(Action)
      } else {
        val = parseInt(val) +1
      }
      var res2 = await database().ref(ref).update({
        count: val,
      }).then(async () => {
        var res3 = await PenFetch({FarmId: FarmId, PenId: PenId}).then((pen) => {
          return pen
        })
        return res3
      });
      return res2
    }).catch(error => {
      console.log("Update pig pen Error: ", error);
      return handleError({ message: 'Update Pigs pen Error' })
    })
    return res;
  }
  return handleError({ message: 'Update Pigs pen Error' })
}

async function updateData({FarmId, TagId, PenId, Action}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    if (TagId) {
      //TODO: not implemented yet.. PIGS
    } else {
      var res = await updatePigPen({FarmId: FarmId, TagId: TagId, PenId: PenId, Action:Action}).then((data) => {
        return data
      })
    }
  }
}

export {
  fetch, 
  updatePigPen,
  updateData,
}