import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import PropTypes from 'prop-types'
import { View, Image } from 'react-native'
import { useTheme } from '../Theme'
import { useTranslation } from 'react-i18next'
import { Button, Comment_Module } from './index'
import { navigationRef, isApp } from '../Navigators/Root'
import { Operations, DataRefs } from '../Services/utils/defines'

import { EditData, SetData, ErrorHandler } from '../Store/Firebase/Utils'
import FirebaseWeighing from '../Store/Firebase/Weighing'
import FirebaseComment from '../Store/Firebase/Comment'
import FirebasePen from '../Store/Firebase/Pen'

const OperationsPanel = ({maxWidth, Type }) => {
  const { t } = useTranslation()
  const { Layout, Common, Images } = useTheme()
  const [_View, setView] = useState(0)

  const dispatch = useDispatch()
  const activeData = useSelector(state => state.firebase.activeData?state.firebase.activeData:undefined)
  const penDatas = useSelector(state => activeData&&activeData.pen&&state.firebase.pens&&state.firebase.pens[activeData.pen]?state.firebase.pens[activeData.pen]:undefined)
  const weightCalcs = useSelector(state => activeData&&state.firebase.activeData&&state.firebase.activeData.weightCalc?state.firebase.activeData.weightCalc:undefined)
  
  switch(Type) {
    case 1:
      break;
    case 2:
      return(
        <View style={[Common.border, Layout.fullWidth, { maxWidth: maxWidth }]}>
          <Button Texts={[t('Operations.Weight')]} Actions={[ele => {
            switch(ele.index) {
              case 0:
                dispatch(SetData.action({DataRef:DataRefs.SECTIONS, Data:null}))
                navigationRef.current?.navigate('User', {screen: 'WeighingOperations'})
                break;
            }
          }]} />
          <View style={[{minHeight:20}]}/>
          <Button Texts={[t('Operations.EditPens')]} Actions={[ele => {
            switch(ele.index) {
              case 0:
                dispatch(SetData.action({DataRef:DataRefs.SECTIONS, Data:null}))
                navigationRef.current?.navigate('User', {screen: 'DataEditor'})
                break;
            }
          }]} />
        </View>
      )
      break;
    case 3:
      var Action = 1;
      var _CodeList = false;
      var _Quantity = true;
      var _Weight = true;
      var _Date = true
      switch(_View) {
        case Operations.TREATMENT:
          _Weight = false;
        case Operations.MOVE:
          _CodeList = true;
        case Operations.SOLD:
          Action = -1;
        case Operations.ADD:
          return(
            <Comment_Module maxWidth={maxWidth} Title={t('Operations.OperationTitle')} HaveQuantity={_Quantity} HaveWeight={_Weight} HaveCodeList={_CodeList} HaveDate={_Date} onDone={(res) => {
              if (res.res && res.data) {
                var _DateTime = res.date
                var action = _View
                action = action.charAt(0).toUpperCase() + action.slice(1)
                var quantity = parseInt(res.quantity)
                if (isNaN(quantity)) quantity = 1
                Action = Action * quantity

                var PenCount = penDatas&&penDatas.datas&&penDatas.datas.count?penDatas.datas.count:0

                var weight
                if (res.weight && res.weight !== "") {
                  weight = Math.round(((parseFloat(res.weight)*quantity) * 100)) / 100
                }

                if (weight) {
                  if (_View === Operations.ADD) {
                    var quant = quantity
                    var weight2 = weight
                    if (weightCalcs && weightCalcs.PenWeight) {
                      weight2 += (weightCalcs.PenWeight*PenCount)   //TODO: something .. if past time.. find given times weight.. "currently its only now.."
                      quant += PenCount
                    }
                    weight2 = Math.round((parseFloat(weight2) * 1000)) / 1000
                    console.log(weight2, quant, PenCount, weightCalcs.PenWeight)
                    dispatch(FirebaseWeighing.create({FarmId: activeData.farm, PenId: activeData.pen, Data:{count: quant, time:_DateTime, weight:weight2 } }))
                  }
                }

                if (_View === Operations.MOVE || _View === Operations.TREATMENT) {
                  dispatch(FirebaseComment.create({FarmId: activeData.farm, PenId: activeData.pen, Action:action, Data:res.data, Date:res.date, Quantity:res.quantity, Weight:res.weight, Code:res.code}))
                } else {
                  dispatch(FirebaseComment.create({FarmId: activeData.farm, PenId: activeData.pen, Action:action, Data:res.data, Date:res.date, Quantity:res.quantity, Weight:res.weight}))
                }

                if (_View !== Operations.TREATMENT) {
                  var newQ = PenCount + Action
                  dispatch(FirebasePen.update({FarmId: activeData.farm, PenId: activeData.pen, Pen:{datas:{count: newQ}}}))
                }

                if (weight) {
                  if (_View === Operations.MOVE || _View === Operations.SOLD) {
                    var newQ = PenCount - quantity
                    if (newQ > 0) {
                      var weight2 = weight
                      if (weightCalcs && weightCalcs.PenWeight) {
                        weight2 += (weightCalcs.PenWeight*newQ)
                      }
                      weight2 = Math.round((parseFloat(weight2) * 1000)) / 1000
                      dispatch(FirebaseWeighing.create({FarmId: activeData.farm, PenId: activeData.pen, Data:{count: PenCount, time:_DateTime, weight:weight2 }}))
                    } else {
                      dispatch(FirebaseWeighing.create({FarmId: activeData.farm, PenId: activeData.pen, Data:{count: 1, time:_DateTime, weight:weight }}))
                    }
                  }
                }
              }
              setView(0)
            }}/>
          )
        case Operations.NOTE:
          return(
            <Comment_Module maxWidth={maxWidth} Title={t('Operations.NoteTitle')} HaveDate={true} onDone={(res) => {
              if (res.res && res.data) {
                var action = _View
                action = action.charAt(0).toUpperCase() + action.slice(1)
                
                dispatch(FirebaseComment.create({FarmId: activeData.farm, PenId: activeData.pen, Action:action, Data:res.data, Date:res.date}))
              }
              setView(0)
            }}/>
          )
        default:
          return(
            <View style={[Common.border, Layout.fullWidth, { maxWidth: maxWidth , paddingVertical:10, paddingHorizontal:5}]}>
              <Button Texts={[t('Operations.Weight'), t('Operations.Note')]} Icons={[]} Actions={[ele => {
                switch(ele.index) {
                  case 0:
                    navigationRef.current?.navigate('User', {screen: 'WeighingOperations'})
                    break;
                  case 1:
                    setView(Operations.NOTE)
                    break;
                }
                }]} />
              <View style={{height:20}}/>
              <Button Texts={[t('Operations.AddPig'),t('Operations.MovePig'),t('Operations.Treatment'),t('Operations.SoldPig')]} Icons={[]} Actions={[ele => {
                switch(ele.index) {
                  case 0:
                    setView(Operations.ADD)
                    break;
                  case 1:
                    setView(Operations.MOVE)
                    break;
                  case 2:
                    setView(Operations.TREATMENT)
                    break;
                  case 3:
                    setView(Operations.SOLD)
                    break;
                }
                }]} />
            </View>
          )
      }
      break;
  }
}

OperationsPanel.propTypes = {
  maxWidth: PropTypes.number,
  Type: PropTypes.oneOf([1,2,3]),
}

OperationsPanel.defaultProps = {
  maxWidth: 600,
  Type:2,
}

export default OperationsPanel
