import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  FlatList,
  Linking
} from 'react-native'
import { Input } from 'react-native-elements'
import { LoadingSplash, Brand, Frame, Button, CustomListItem } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { DataRefs } from '../../../Services/utils/defines'
import { ErrorHandler, SetData, ClearData } from '../../../Store/Firebase/Utils'
import FirebaseFarm from '../../../Store/Firebase/Farm'

const SystemBoard = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const farmState = useSelector(state => state.firebase.farms)
  const [FarmSelect, setFarmSelect] = useState({name:""})

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
    dispatch(FirebaseFarm.action({FarmId: null}))
  },[])

  if (farmState && Object.keys(farmState).length > 0){
    return(
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Layout.colCenter, Common.backgroundPrimary]}>
        <FlatList
          style={[Gutters.smallVPadding, Gutters.smallHPadding]}
          data={Object.keys(farmState)}
          renderItem={({item, index})=>{
            var Farm = {...farmState[item]};
            if (!Farm) return null
            Farm.id = item
            return(
              <View style={[Layout.fill, Layout.rowCenter, Layout.fullWidth]}>
                <CustomListItem Title={Farm.name} itemStyle={[{minWidth: 100, height:35}]} onPress={() => {
                    //this.props.setData(item, Section, Pen)
                    //NavigationService.navigate("DataDisplay")
                  }}/>
                <CustomListItem itemStyle={[{minWidth:150, height:35, justifyContent:"center"}]} Title={Farm.manager} onPress={() => {
                    setFarmSelect(Farm)
                  }}/>
                <CustomListItem itemStyle={[{minWidth:150, height:35}]} Title={Farm.email} onPress={() => {
                    setFarmSelect(Farm)
                  }}/>
              </View>
            )
          }}
          keyExtractor={(item, index) => index.toString()}
        />
        <View style={[Layout.fillRow, Layout.fullWidth, {height:70, justifyContent:'center'}]}>
          <Input style={[Common.textInput, {height:40, width:160}]}
            containerStyle={[Layout.quarterWidth,{width:180, height:70, borderWidth:1, marginTop:10}]}
            inputStyle={{paddingHorizontal:5}}
            label={"Farm-tag:"}
            onChangeText={text => 
              setFarmSelect(text)
            }
            editable={false}
            keyboardType={'default'}
            value={FarmSelect.name}
            selectTextOnFocus
          />
          <View style={[Layout.fillRow, Layout.quarterWidth, {height:50, paddingTop:20, marginHorizontal:10, maxWidth:220, justifyContent:'space-between'}]}>
            <Button Texts={["Consys"]} maxWidth={90}
              Actions={[() => {
                if (FarmSelect.site) {
                  var Cloud = "http://" + FarmSelect.site + ".agrisys.cloud/"
                  Linking.openURL(Cloud)
                }
              }]} />
            <Button Texts={["Farm Panel"]} maxWidth={110}
              Actions={[() => {
                if (FarmSelect.id !== "") {
                  //dispatch(ClearData.action({DataRef:[DataRefs.USERSDATA,DataRefs.ACTIVE, DataRefs.FARMS]}))
                  dispatch(SetData.action({DataRef:DataRefs.FARMS, Data:FarmSelect.id}))
                  navigationRef.current?.navigate('User', {screen: 'Home'})
                }
              }]}/>
          </View>
        </View>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default SystemBoard
