import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

import { DataRefs, DataStates } from '../utils/defines'
import { update as PenUpdate, fetch as PenFetch } from './Pens'
import { fetch as CommentFetch } from './Comments'
import { fetch as WeightFetch } from './Weighings'

async function fetch({FarmId, BatchId, Data, DataTime}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    if (_rend.getState().firebase.batchs) {
      var time = await database().ref(ref + '/update_time').once('value').then((_timeData) => {
        if (_rend.getState().firebase.batchs.update_time < _timeData.val()) return false
        else return true
      })
      if (time) {
        var StateBatchs = _rend.getState().firebase.batchs
        if (!BatchId && !StateBatchs[BatchId]) {
          return StateBatchs
        }
      }
    }

    if (BatchId){
      ref += "/" + BatchId
    }

    var res
    if (!Data) {
      res = await database().ref(ref).once('value').then((_batchdata) => {
        if (BatchId) {
          var batchdata = _batchdata.val()
          if (!batchdata) return null;
          if (!batchdata.datas) batchdata.datas = {}
          if (!batchdata.pens) batchdata.pens = []
          if (!batchdata.datas.gcurve) batchdata.datas.gcurve = "default";

          var batch = {[BatchId]: batchdata}
          var state = _rend.getState()
          if (state.firebase.batchs){
            batch = {...state.firebase.batchs}
            batch[BatchId] = batchdata
          }
          return batch
        } else {
          return _batchdata.val()
        }
      })
    }
    else if (BatchId) {
      switch(Data){
        case DataRefs.BATCHS:
          ref += "/" + "datas"
          res = await database().ref(ref).once('value').then((_batchdata) => {
            var data = {[BatchId]: {datas: _batchdata.val()}}
            return data
          })
          break;
        case DataRefs.PENS:
          ref += "/" + "pens"
          res = await database().ref(ref).once('value').then((_batchdata) => {
            var pens = {[BatchId]: {pens: _batchdata.val()}}
            return pens
          })
          break;
        case DataRefs.GRAPH:
          ref += "/" + "gcurve"
          res = await database().ref(ref).once('value').then((_batchdata) => {
            var curve = {[BatchId]: {gcurve: _batchdata.val()}}
            return curve
          })
          break;
      }
    }
    if (res) return res
  }
  return handleError({ message: 'Fecth Batchs Error' })
}

async function update({FarmId, BatchId, Data}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    if (BatchId && Data) {
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      ref += "/" + BatchId
      var res = await fetch({FarmId: FarmId, BatchId: BatchId, Data:DataRefs.PENS}, _rend).then(async (_batch) => {
        var Pens = _batch[BatchId].pens
        var BatchPens = {}

        if (Data.pens && JSON.stringify(Data.pens)!==JSON.stringify(Pens)) {
          if (!Pens) Pens = []
          var RemovedPens = Object.keys(Pens).filter(x => !Object.keys(Data.pens).includes(x) && !Pens[x].enddate);
          var NewPens = Object.keys(Data.pens).filter(x => !Object.keys(Pens).includes(x) || Pens[x].enddate);
          console.log(NewPens,RemovedPens)
          if (RemovedPens && RemovedPens.length > 0) {
            RemovedPens.forEach(pen => {
              if (Pens[pen]) {
                var Pen = Pens[pen]
                if (!Pen.enddate) { Pen = { } }
                Pen.enddate = Date.now()
                BatchPens[pen] = Pen
                PenUpdate({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:""}}},_rend)
              }
            })
          }
          if (NewPens && NewPens.length > 0) {
            NewPens.forEach(pen => {
              var Pen = Pens[pen]?Pens[pen]:{startdate:Date.now()}
              if (Pen.enddate) delete Pen.enddate
              if (!Pen.startdate) Pen.startdate = Date.now()
              BatchPens[pen] = Pen
              PenUpdate({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:BatchId}}},_rend)
            })
          }

          var SamePens = Object.keys(Pens).filter(x => !RemovedPens.includes(x) && !NewPens.includes(x));
          if (SamePens && SamePens.length > 0) {
            SamePens.forEach(pen => {
              BatchPens[pen] = Pens[pen]
            })
          }
        }
        Data.pens = BatchPens

        if (Data.state && Data.state === DataStates.CLOSED) {
          var res2 = await fetch({FarmId: FarmId, BatchId: BatchId, Data:DataRefs.GRAPH}, _rend).then(async (curve) => {
            var CurveId = curve[BatchId].gcurve
            if (_rend.getState().firebase.graphs[CurveId]) {
              Data.gcurve = _rend.getState().firebase.graphs[CurveId]
              Data.closed = {comments: [], weighings: []}
              await Promise.all(Object.entries(Pens).map(async ([PenId,PenData]) => {
                Data.pens[PenId] = PenData.enddate?PenData:{}
                if (!Data.pens[PenId].enddate) {
                  Data.pens[PenId].enddate = Date.now()
                }
                /*await PenFetch({FarmId: FarmId, PenId:PenId}, _rend).then((pendata) => {
                  var count = 0
                  console.log(pendata)
                  if (pendata[PenId] && pendata[PenId].datas && pendata[PenId].datas.count) count = pendata[PenId].datas.count
                  Data.pens[PenId].count = count
                })*/
                
                PenUpdate({FarmId:FarmId, PenId:PenId, Pen:{datas:{batch:"",count:0}}}, _rend)
                await CommentFetch({FarmId: FarmId, PenId: PenId}, _rend).then((comments) => {
                  Data.closed.comments[PenId] = comments[PenId]
                  database().ref("farms_data/" + FarmId + "/datas/comments/" + PenId).remove()
                })
                await WeightFetch({FarmId: FarmId, PenId: PenId}, _rend).then((weights) => {
                  Data.closed.weighings[PenId] = weights[PenId]
                  Data.pens[PenId].count = weights[PenId][Object.keys(weights[PenId])[Object.keys(weights[PenId]).length-1]].count
                  database().ref("farms_data/" + FarmId + "/weighings/" + PenId).remove()
                })
              }))
              database().ref("farms_data/" + FarmId + "/datas/pens").update({
                update_time: database.ServerValue.TIMESTAMP
              })
              database().ref("farms_data/" + FarmId + "/datas/comments").update({
                update_time: database.ServerValue.TIMESTAMP
              })
              database().ref("farms_data/" + FarmId + "/datas/weighings").update({
                update_time: database.ServerValue.TIMESTAMP
              })

              console.log(Data)
              var res3 = await database().ref(ref).update({
                ...Data
              }).then(async ()=>{
                var res4 = await database().ref(ref + "/closed").set(
                  Data.closed
                ).then(async () => {
                  var res5 = await fetch({FarmId: FarmId, BatchId: BatchId}, _rend).then((data)=>{
                    return data
                  })
                  return res5
                }).catch(error => {
                  if (error.code === 'database/permission-denied') {
                    console.log("Batch update was permission denied. ");
                  }else if (error.code) {
                    console.log("Batch update Error: ", error);
                  }
                  console.log(error)
                  resolve({error:"An error occurred"})
                })
                return res4
              })
              return res3
            }else {
              return handleError({ message: 'Close Batchs, unknown CurveId' })
            }
          })
          return res2
        } else {
          var res2 = await database().ref(ref).update({
            ...Data,
          }).then(async () => {
            var res3 = await fetch({FarmId: FarmId, BatchId: BatchId, DataTime: "SET"}, _rend).then((data)=>{
              return data
            })
            return res3
          }).catch(error => {
            if (error.code === 'database/permission-denied') {
              console.log("Batch update was permission denied. ");
            }else if (error.code) {
              console.log("Batch update Error: ", error);
            }
            console.log(error)
            return  handleError({ message: 'Update Batchs Error' })
          })
          return res2
        }
      })
      return res
    }
  }
  return handleError({ message: 'Update Batchs Error' })
}

async function create({FarmId, BatchId, Data}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    if (BatchId && Data) {
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      ref += "/" + BatchId
      var res = await database().ref(ref).set({
        ...Data,
      }).then(async () => {
        await Promise.all(Object.keys(Data.pens).map(pen => {
          PenUpdate({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:BatchId}}},_rend)
        }))
        var res2 = await fetch({FarmId: FarmId, DataTime: "SET"}, _rend).then((data)=>{
          return data
        })
        return res2
      }).catch(error => {
        if (error.code === 'database/permission-denied') {
          console.log("Batch update was permission denied. ");
        }else if (error.code) {
          console.log("Batch create Error: ", error);
        }
        return handleError({ message: 'Create Batchs Error' })
      })
      return res
    }
  }
  return handleError({ message: 'Create Batchs Error' })
}

export {
  fetch, 
  update,
  create,
}