import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  ActivityIndicator,
  Text,
  TouchableOpacity,
  Image,
  ScrollView,
} from 'react-native'
import { Brand, Frame, Button } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import MenuView from './MenuView'
import DashBoard from './DashBoard'
import Pens from './Pens'
import Batchs from './Batchs'
import GrowthCurves from './GrowthCurves'
import Operations from './Operations'
import Autopigs from './Autopigs'

import { DataRefs } from '../../../Services/utils/defines'
import FirebaseFarm from '../../../Store/Firebase/Farm'
import { SetData } from '../../../Store/Firebase/Utils'

const IndexUserContainer = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const [isAppView, setIsAppView] = useState(null)
  const [currentView, setView] = useState(null)

  const activeFarm = useSelector(state => {
    if (state.firebase.activeData && state.firebase.activeData.farm) return state.firebase.activeData.farm
    else if (state.firebase.user&&state.firebase.user.farms) {
      var FarmId = Object.keys(state.firebase.user.farms)[0]
      dispatch(SetData.action({DataRef:DataRefs.FARMS, Data:FarmId}))
      return FarmId
    }
    else return undefined
  })
  const farmDatas = activeFarm?useSelector(state => state.firebase.farms?state.firebase.farms[activeFarm]:undefined):undefined

  useEffect(() => {
    if (activeFarm) {
      dispatch(FirebaseFarm.action({FarmId: activeFarm}))
    }
  },[activeFarm])

  useEffect(() => {
    if (isApp() != isAppView)setIsAppView(isApp)
  })

  const changeView = ({ View }) => {
    if (isAppView) {
      switch(View){
        case "DashBoard":
        case "Pens":
        case "Batchs":
        case "Operations":
          navigationRef.current?.navigate('User', {screen: View})
          break;
        case "Growth":
          navigationRef.current?.navigate('User', {screen: "GrowthCurves"})
          break;
        case "Autofunctions":
          navigationRef.current?.navigate('User', {screen: "Autopigs"})
          break;
      }
    }
    else {
      switch(View){
        case "DashBoard":
          setView([(<DashBoard key="View_Board"/>)])
          break;
        case "Pens":
          setView([(<Pens key="View_Board"/>)])
          break;
        case "Batchs":
          setView([(<Batchs key="View_Board"/>)])
          break;
        case "Growth":
          setView([(<GrowthCurves key="View_Board"/>)])
          break;
        case "Operations":
          setView([(<Operations key="View_Board"/>)])
          break;
        case "Autofunctions":
          setView([(<Autopigs key="View_Board"/>)])
          break;
      }
    }
  }

  if (isAppView === null){
    return(<ActivityIndicator />)
  } else if (isAppView) {
    if (currentView) return currentView
    else return (<MenuView setView={changeView} />)
  } else {
    if (!currentView) {
      setView([(<DashBoard key="View_Board"/>)])
    }
    return(
      <View style={[Layout.fillRow, Layout.fullSize, Common.backgroundPrimary, {alignItems:'flex-start'}]}>
        <View style={[Layout.fillCol, Layout.fullHeight, {minWidth:300, maxWidth:320, borderRightWidth:1}]}>
          {/* Menu */}
          <View style={[Layout.fill]}>
            <MenuView setView={changeView} />
          </View>
          <View style={[Layout.fullWidth, {height: 110, marginBottom:15}]}>
            <Image style={[Layout.fullSize]} source={Images.Logo} resizeMode={'contain'} />
          </View>
        </View>
        <View style={[Layout.fillCol, Layout.fullSize, {alignItems:'flex-start' }]}>
          {/* View */}
          {currentView}
        </View>
      </View>
    )
  }
}

export default IndexUserContainer
