import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import { createAction } from '@reduxjs/toolkit'
import { FirebaseInitialState } from './index'
import { CurvePredict, WeightCalc } from '../../Services/Firebase/Utils'
import handleError from '../../Services/utils/handleError'

import { DataRefs, DataStates } from '../../Services/utils/defines'

const ErrorHandler = {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/setError', async(message="",data="",status="")=> {
    return handleError(message,data,status)
  }),
  reducers: buildAsyncReducers({
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}

const SetData = {
  initialState: {},
  action: createAction('firebase/setData'),
  reducers(state, { payload }) {
    if (!state.activeData) state.activeData = {weightCalc:{} }
    if (typeof payload.DataRef !== 'undefined' && typeof payload.Data !== 'undefined') {
      switch(payload.DataRef) {
        case DataRefs.FARMS:
          state.activeData = {weightCalc:{}, farm:payload.Data }
          state.pens = null
          state.sections = null
          state.batchs = null
          state.comments = null
          state.graphs = null
          state.weighings = null
          break;
        case DataRefs.BATCHS:
          state.activeData.batch = payload.Data
          break;
        case DataRefs.SECTIONS:
          state.activeData.section = payload.Data
          state.activeData.pen = null
          break;
        case DataRefs.PENS:
          state.activeData.pen = payload.Data
          break;
      }
    }
  }
}

const EditData = {
  initialState: {},
  action: createAction('firebase/editData'),
  reducers(state, { payload }) {
    if (typeof payload.DataRef !== 'undefined' && typeof payload.Data !== 'undefined') {
      var Data = payload.Data
      switch(payload.DataRef) {
        case DataRefs.ACTIVE:
          if (typeof payload.Append !== 'undefined' && payload.Append) {
            state.activeData = {
              ...state.activeData,
              ...Data
            }
          } else state.activeData = Data
          if (!state.activeData.weightCalc) state.activeData.weightCalc = {}
          break;
        case DataRefs.FARMS:
          if (typeof payload.Append !== 'undefined' && payload.Append) {
            state.farms = {
              ...state.farms,
              ...Data
            }
          } else state.farms = Data
          break;
      }
    }
  },
}

const ClearData={
  initialState: {},
  action: createAction('firebase/clearData'),
  reducers(state, { payload }) {
    console.log("test")
    if (typeof payload.DataRef !== 'undefined') {
      const Clears = Object.values(DataRefs).filter(x => !Object.values(payload.DataRef).includes(x));
      var test = {...state}
      console.log(test)
      Object.values(Clears).forEach(DataRef => {
        switch(DataRef){
          case DataRefs.ACTIVE:
            break;
          case DataRef.PENS:
            test.pens = {}
            break;
          case DataRef.SECTIONS:
            test.sections = {}
            break;
        }
      });
      console.log(test)
    }
  }
}

const WeightCalculate = {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/weightCalc', WeightCalc),
  reducers: buildAsyncReducers({
    itemKey: null,
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}

export {
  ErrorHandler,
  SetData,
  EditData,
  ClearData,
  WeightCalculate,
}