import { DataRefs, DataStates } from '../../Services/utils/defines'

function AccumulateTime(Time, PeriodeLength, Times = 1) { //Todo... should only be days now..
  var NewTime = new Date(Time.getTime())
  switch(PeriodeLength) {
    case "Weeks":
      NewTime.setDate(NewTime.getDate() + 7)
      break;
    case "Month":
      NewTime.setMonth(NewTime.getMonth() + 1)
      break;
    case "Days":
    default:
      //86.400.000 ms
      do {
        NewTime.setDate(NewTime.getDate() + 1)
        Times--
      } while(Times > 0)
      break;
  }
  return NewTime;
}
//Get Slope Between two points
function GetSlope_Between(MinMax_Time, MinMax_Data){
  var minX = MinMax_Time[0]
  var maxX = MinMax_Time[1]
  var minY = MinMax_Data[0]
  var maxY = MinMax_Data[1]

  //Slope = DeltaY / DeltaX
  var slope = (maxY - minY) / (maxX - minX)
  if (minY > minX) slope = -slope;
  return slope
}

//Get point expected data and virtual simulation from data.
//ExValue = Expected value for AtTime, if slope continues, for spots given (or inbetween).
//ExTime = Expected epoch time, there slope for given spots, will be ForData
export function get_DataSpot(MinMax_Time, MinMax_Data, AtTime, StartVal, ForData) {
  var ExValue = null
  var ExTime = null
  if (!StartVal || StartVal === null) StartVal = MinMax_Data[0]

  var slope = GetSlope_Between(MinMax_Time, MinMax_Data)
  if (AtTime) {
    var FindX = AtTime- MinMax_Time[0]
    // ExValue = (FindX * Slope) + MinY               // Setting FindTime, due b then = Min data.. (b is y = 0)
    ExValue = (FindX * slope) + StartVal          // ( X * a ) + b = Expeted data for spot on CalcTime
  }
  if (ForData) {
    ExTime = (ForData - StartVal) / slope          // (y-b)/a = Expeted time for data
    
    ExTime = MinMax_Time[0] + ExTime
    ExTime = Math.floor(ExTime)
  }

  return {ExValue, ExTime}
}

/* Shared functions */
function isBatchClosed(Batch){
  return (Batch && Batch.state && Batch.state === DataStates.CLOSED)?true:false
}
function getBatchWeights(Batch){
  return (Batch && Batch.closed && Batch.closed.weighings)?Batch.closed.weighings:null
}
function GetBatchEnd(Batch, Curve) {
  if (Batch && Batch.time && Curve) {
    var currentTime = new Date(Batch.time) //Does not allow weight's after Batch end
    var CurveLenght = Object.keys(Curve.data)[Object.keys(Curve.data).length-1]
    currentTime = new Date(AccumulateTime(currentTime, Curve.periode, CurveLenght+1))
    return currentTime.getTime();
  }
  return null
}
function GetNewestWeight({Weighings, AfterTime, BeforeTime}) {
  var Weighing = null;
  if (Weighings) {
    for (const _weighing in Weighings) {
      var weighing = WeighingsStore[_weighing]
      if (weighing && !weighing.deleted && !weighing.edited && (!Weighing || Weighing.time < weighing.time)) {
        if ((!AfterTime || weighing.time >= AfterTime) &&
          (!BeforeTime || weighing.time <= BeforeTime)) {
          Weighing = weighing
        }
      }
    }
  }
  return Weighing;
}
function getCurveData({Graphs, Batch}) {
  if (Batch && Graphs && !Graphs.data) {
    if (isBatchClosed(Batch) && Batch.gcurve) return Batch.gcurve;
    else if (Batch.gcurve && Graphs && Graphs[Batch.gcurve]) return Graphs[Batch.gcurve]
  } 
  else if (Graphs && Graphs.data) return Graphs
  return null
}
/* ****************** */

function getLatestWeight(Weighings, AfterTime = null, BeforeTime = null){
  var Weighing = null;
  if (Weighings) {
    Object.entries(Weighings).forEach(async ([WeightId, WeightData]) => {
      if (WeightData && !WeightData.deleted && !WeightData.edited && (!Weighing || Weighing.time < WeightData.time)) {
        if ((!AfterTime || WeightData.time >= AfterTime) &&
          (!BeforeTime || WeightData.time <= BeforeTime)) {
          Weighing = WeightData
        }
      }
    })
  }
  return Weighing
}

async function CurvePredict({ Weighings, Batch, Curve, Graphs}) {
  if (!Curve) Curve = getCurveData({Graphs, Batch})
  if (Curve && Curve.data && Batch && Batch.time) {
    var Target = (Batch&&Batch.datas&&Batch.datas.target)?Batch.datas.target:Curve.data[Curve.data.length-1]
    var BatchEndDate = GetBatchEnd(Batch, Curve)
    var Weighing = getLatestWeight(Weighings, Batch.time, BatchEndDate)
    if (Weighing && Weighing.weight) {
      var weight = Weighing.weight
      if (Weighing.count) weight = weight / Weighing.count
      
      var weight2 = 0
      var Weighing2 = getLatestWeight(Weighings, Batch.time, Weighing.time -1)
      if (Weighing2) {
        weight2 = Weighing2.weight
        if (Weighing2.count) weight2 = weight2 / Weighing2.count
      }

      var StartIndex = "none";
      var PredictCurve = { 
        periode: Curve.periode,     //Periode [] ... should only be days --- todo: change
        data: [],                   //Curve for perdict
        time: [],
        Now: null,                  //Current weight
        TempEx: null,               //Expected current value read from template
        lastWeighing: Weighing,
        TargetForcast: null,
      }
      
      var CurveLenght = parseInt(Object.keys(Curve.data)[Object.keys(Curve.data).length-1])
      var StartTime = new Date(Batch.time) // accumulated by index step to first predict point..
      var lastIndex = 0
      var lastIndexTime = StartTime
      Object.entries(Curve.data).forEach(([_index,Data]) => {
        var index = parseInt(_index)
        if (lastIndex != index && !PredictCurve.TempEx) {
          var nextIndexTime = AccumulateTime(lastIndexTime, Curve.periode, index-lastIndex)
          if (nextIndexTime >= new Date(Date.now()).setHours(0,0,0,0)) {
            var data = get_DataSpot([lastIndexTime, nextIndexTime], [Curve.data[lastIndex], Curve.data[index]], new Date(Date.now()))
            PredictCurve.TempEx = data.ExValue
          }
          lastIndexTime = nextIndexTime
        }
        lastIndex = index
      });

      var CurrentTimeStep = new Date(Weighing.time);
      var lastIndex = 0
      var nextIndex = 0
      var lastIndexTime = CurrentTimeStep
      var CurveData = Object.keys(Curve.data).values();
      /* 1. find template fit for weight or weights slope*/
      // rem: curve could contain 0 > than weight and indexed [0,10,20...]
      for (var index = 0; index < CurveLenght; index++) {
        var next_CurrentTimeStep = new Date(AccumulateTime(CurrentTimeStep, Curve.periode))
        var _weight = PredictCurve.data.length?PredictCurve.data[PredictCurve.data.length-1]:weight
        var CurvePointData = Curve.data[index]
        if (CurvePointData && CurvePointData < _weight) CurvePointData = null
        if (!CurvePointData && index > 0) { //if curve is index [0,10,20] find data for current "missing" index
          var nextIndex = index -1
          var obj
          do {
            nextIndex++
            obj = Curve.data[nextIndex]
          } while (!obj && nextIndex < CurveLenght)
          if (obj > _weight) {
            var nextIndexTime = AccumulateTime(lastIndexTime, Curve.periode, nextIndex-lastIndex)

            var next_CurrentTimeStep2 = new Date(AccumulateTime(lastIndexTime, Curve.periode))
            var data = get_DataSpot([lastIndexTime.getTime(), nextIndexTime.getTime()], [Curve.data[lastIndex], Curve.data[nextIndex]], next_CurrentTimeStep2.getTime(), _weight, Curve.data[nextIndex])
            
            CurvePointData = data.ExValue
          } else {
            lastIndex = nextIndex
            lastIndexTime = AccumulateTime(lastIndexTime, Curve.periode, nextIndex-lastIndex)
            index = nextIndex
          }
        }else {
          lastIndex = index
          lastIndexTime = CurrentTimeStep
        }

        if (CurvePointData && weight < CurvePointData) {
          if (StartIndex === "none") {
            if (index === 0) {
              var Data
              if (Weighing2) {
                Data = get_DataSpot([Weighing2.time, Weighing.time], [weight2, weight], next_CurrentTimeStep)
              }
              else {
                var time = AccumulateTime(new Date(Weighing.time), Curve.periode, Object.keys(Curve.data)[1])
                Data = get_DataSpot([Weighing.time, time.getTime()], [Curve.data[0], Curve.data[1]], next_CurrentTimeStep, weight)
              }
              Data.Weighing = {weight: Data.ExValue, time: next_CurrentTimeStep.getTime()}
              PredictCurve.data.push(Data.Weighing.weight)
              PredictCurve.time.push(Data.Weighing.time)

              Weighing2 = Weighing
              weight2 = weight
              Weighing = Data.Weighing
              weight = Data.ExValue
              
              StartIndex = "none"
              //index--
            }else {
              StartIndex = index
            }
          } else {
            PredictCurve.data.push(CurvePointData)
            PredictCurve.time.push(CurrentTimeStep.getTime())
          }
          if (CurrentTimeStep >= new Date(Date.now()).setHours(0,0,0,0)) {
            if (!PredictCurve.Now) {
              if (CurrentTimeStep === new Date(Date.now()).setHours(0,0,0,0)) PredictCurve.Now = PredictCurve.data[PredictCurve.data.length-1]
              else {
                var Data = get_DataSpot([PredictCurve.time[PredictCurve.time.length-3], PredictCurve.time[PredictCurve.time.length-2]], [PredictCurve.data[PredictCurve.data.length-3], PredictCurve.data[PredictCurve.data.length-2]], new Date(Date.now()))
                PredictCurve.Now = Data.ExValue
              }
            }
          }
          StartTime = new Date(AccumulateTime(StartTime, Curve.periode))
          CurrentTimeStep = next_CurrentTimeStep
          if (CurvePointData <= Batch.datas.target) {
            PredictCurve.TargetForcast = CurrentTimeStep.getTime()
          }
        }
      }
      
      return PredictCurve;
    }
  }
  return null;
}

async function WeightCalc({ Weighings, StartDate, EndDate, Batch, Graphs, Display=false}, _rend) {
  //var state = _rend.getState()
  var PenWeight = 0
  var PenGrowth = 0

  var LastWeight = undefined;
  var LastWeightDay = undefined;
  var FirstWeightDay = undefined;
  var today = false

  var isVirtual = null
  
  var isClosed = false
  if (!Weighings && !isBatchClosed(Batch)) {
    Weighings = getBatchWeights(Batch)
  }

  if (Weighings) {
    var GrowthCount = 0
    Weighings = Object.fromEntries(Object.entries(Weighings).sort(([,a],[,b]) => a.time<b.time?-1:1))
    
    Object.entries(Weighings).forEach(([Id,Weighing]) => {
      if (!Weighing.deleted && !Weighing.edited && ((!StartDate || !EndDate) || (Weighing.time >= StartDate && Weighing.time <= EndDate))) {
        var weight = Weighing.weight
        if (Weighing.count) weight = weight / Weighing.count;
        PenWeight = weight
        if (LastWeight !== undefined) {
          var weight = PenWeight - LastWeight
          PenGrowth += weight
          GrowthCount++;
        }
        
        LastWeight = PenWeight
        LastWeightDay = Weighing.time
        if ( new Date(LastWeightDay).setHours(0,0,0,0) === new Date(Date.now()).setHours(0,0,0,0) ) today = true;
        if (!FirstWeightDay) FirstWeightDay = Weighing.time;
      }
    })
  }

  if (Batch) {
    var Predict = await CurvePredict({Weighings, Batch, Graphs})
    isVirtual = Predict
    if (Predict && Predict.Now && Date.now() >= new Date(LastWeightDay).getDate()) {
      PenWeight = Predict.Now
      if (LastWeight !== undefined) {
        var weight = PenWeight - LastWeight
        PenGrowth += weight
        GrowthCount++;
      }
      LastWeight = PenWeight
      LastWeightDay = Date.now()
    }
    //console.log("??", Predict)
  }

  if (GrowthCount > 0) {
    var days = (LastWeightDay - FirstWeightDay) / (1000*60*60*24)
    PenGrowth = PenGrowth/days
  }
  //console.log(isVirtual)
  return { PenWeight, PenGrowth, Batch, isVirtual }
}

export {
  AccumulateTime,
  CurvePredict,
  WeightCalc,
}