import AsyncStorage from '@react-native-async-storage/async-storage'
import { combineReducers } from 'redux'
import {
  persistReducer,
  persistStore,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist'
import { configureStore } from '@reduxjs/toolkit'

import startup from './Startup'
//import user from './User'
import firebase from './Firebase'
import theme from './Theme'

const firebasePersistConfig = {
  key: 'firebase',
  storage: AsyncStorage,
  blacklist: ['loading', 'error', 'activeData', 'farms', 'batchs', 'sections', 'pens', 'graphs', 'autopigs', 'weighings', 'comments'],
}

const reducers = combineReducers({
  startup,
  //user,
  firebase: persistReducer(firebasePersistConfig, firebase),
  theme,
})

const rootPersistConfig = {
  key: 'root',
  storage: AsyncStorage,
  blacklist: ['startup', 'firebase'],
}

const persistedReducer = persistReducer(rootPersistConfig, reducers)

const store = configureStore({
  reducer: persistedReducer,
  middleware: getDefaultMiddleware => {
    const middlewares = getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
      },
    })

    if (__DEV__ && !process.env.JEST_WORKER_ID) {
      const createDebugger = require('redux-flipper').default
      middlewares.push(createDebugger())
    }

    return middlewares
  },
})

const persistor = persistStore(store)

export { store, persistor }
