import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

async function signin({Email, Pass}) {
  if (auth().currentUser){
    if (auth().currentUser.email != Email) {
      await logout()
    }
    else {
      return await getUserData()
    }
  }
  var res = await auth().signInWithEmailAndPassword(Email,Pass).then(async (e) => {
    var User = await getUserData()
    return User
  }).catch(error => {
    console.log("User Signin Error: ", error);
    if (error.code === 'auth/invalid-email') {
      return handleError({ message: 'The email addres is invalid' })
    }
    return handleError({ message: 'Could not singin' })
  })
  return res
}

async function logout() {
  var res = await auth().signOut().then(() => {
    console.log("logout")
    return null
  }).catch(async error => {
    console.log("User logout error:", error);
    return await getUserData()
  })
  return res
}

async function resetPassword({Email}) {
  var res = await auth().sendPasswordResetEmail(Email).then(() => {
    return null
  }).catch(error => {
    console.log("User resetPass error:", error);
    if (error.code === 'auth/user-not-found') {
      //Should not exploid the users... (this could be used as a find user-mails)
    }
    return handleError({ message: 'Could not send reset' })
  })
  return res
}

async function getUserData() {
  if (auth().currentUser) {
    var Data =(({ 
      displayName, email, emailVerified, phoneNumber, photoURL, uid, refreshToken
    }) => ({ 
      displayName, email, emailVerified, phoneNumber, photoURL, uid, refreshToken
    }))(auth().currentUser);

    if (auth().currentUser.metadata) {
      const _Meta = (({ 
        creationTime, lastSignInTime
      }) => ({ 
        creationTime, lastSignInTime,
      }))(auth().currentUser.metadata)
      Data.metadata = _Meta
    }

    if (auth().currentUser.uid){
      var Data2 = await fetch(Data.uid)
      Data = {...Data, ...Data2}
    }
    return Data
  }
  else return null
}

async function fetch(UserId, List = false) {
  var user = "";
  if (UserId) user = UserId
  else if (!List && auth().currentUser) user = auth().currentUser.uid;

  if (user || List) {
    const ref = "users/" + user;
    var userData = await database().ref(ref).once('value').then((userData) => {
      var data = userData.val()
      return data
    }).catch(error => {
      console.log(error)
      if (error.code === 'database/permission-denied') {
        console.log("User update was permission denied. ");
      }else if (error.code) {
        console.log("User update Error: ", error);
      }
      return handleError({ message: 'User fetch, An error occurred' })
    })
    return userData;
  }
  else return handleError({ message: 'User uid in fecth unknown' })
}

