import { buildSlice } from '@thecodingmachine/redux-toolkit-wrapper'
import { ErrorHandler, SetData, EditData, ClearData, WeightCalculate } from './Utils'
import User from './User'
import Farm from './Farm'
import Batch from './Batch'
import Graph from './Graph'
import Section from './Section'
import Pen from './Pen'
import Pig from './Pig'
import Weighing from './Weighing'
import Comment from './Comment'

// This state is common to all the "user" module, and can be modified by any "user" reducers
export const FirebaseInitialState = {
  activeData: {weightCalc:null},
  user: null,
  farms: null,
  batchs: null,
  graphs: null,
  sections: null,
  pens: null,
  weighings: null,
  comments: null,
}

export default buildSlice('firebase', [ErrorHandler, SetData, EditData, ClearData, WeightCalculate, User, Farm, Batch, Graph, Section, Pen, Pig, Weighing, Comment], FirebaseInitialState).reducer
