import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update, updateData, create} from '../../Services/Firebase/Comments'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/comments', fetch),
  //update: buildAsyncActions('firebase/comments', update),
  create: buildAsyncActions('firebase/comments', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'comments',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}