import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, updateData, updatePigPen} from '../../Services/Firebase/Pigs'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/pigs', fetch),
  updateData: buildAsyncActions('firebase/pigs', updateData),
  updatePigPen: buildAsyncActions('firebase/pigs', updatePigPen),
  
  reducers: buildAsyncReducers({
    itemKey: 'pigs',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}