import React from 'react'
import { createStackNavigator } from '@react-navigation/stack'
import { createDrawerNavigator } from '@react-navigation/drawer';
import { IndexAuthContainer, CreateUserContainer } from '../Containers'
import { SafeAreaView, StatusBar } from 'react-native'
import { useTheme } from '../Theme'
import { useTranslation } from 'react-i18next'

const AuthStack = createStackNavigator();

// @refresh reset
const AuthNavigator = () => {
  const { t } = useTranslation()
  const { Layout, darkMode, NavigationTheme } = useTheme()
  const { colors } = NavigationTheme
  return (
    <SafeAreaView style={[Layout.fill, { backgroundColor: colors.card }]}>
      <AuthStack.Navigator headerMode={'screen'} initialRouteName={"Home"} mode={"modal"}
        screenOptions={{
          title: 'AgrisysApp',
          headerTintColor: 'white',
          headerTitleAlign: 'center',
      }}>
        <AuthStack.Screen name="Home" component={IndexAuthContainer} 
          options={({ navigation }) => ({
            title: t('AgrisysConnect.NavTitle'),
          })}
        />
        <AuthStack.Screen name="CreateUser" component={CreateUserContainer} 
          options={({ navigation }) => ({
            title: t('AgrisysConnect.NavTitle'),
          })}
        />
      </AuthStack.Navigator>
    </SafeAreaView>
  )
}

export default AuthNavigator
