import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

import { sortObject } from '../utils/formators'

async function fetch({FarmId, SectionId}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/sections";
    if (_rend.getState().firebase.sections) {
      var time = await database().ref(ref + '/update_time').once('value').then((_timeData) => {
        if (_rend.getState().firebase.sections.update_time < _timeData.val()) return false
        else return true
      })
      if (time) {
        console.log("time")
        var StateSections = _rend.getState().firebase.sections
        if (!SectionId && !StateSections[SectionId]) {
          return StateSections
        }
      }
    }

    if (SectionId){
      ref += "/" + SectionId
    }
    var res = await database().ref(ref).once('value').then((_sectionidData) => {
      var Sections = sortObject(_sectionidData.val())
      Sections.update_time = _sectionidData.val().update_time
      return Sections
    })
    return res
  }
  return handleError({ message: 'Fecth Sections Error' })
}

async function update({FarmId, SectionId, Data}, _rend) {

}

export {
  fetch,
  update,
}