import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  ScrollView,
  Picker,
  CheckBox
} from 'react-native'
import { Input } from 'react-native-elements'
import { LoadingSplash, Brand, Frame, Button, ContentProp, Datetime_Picker } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { sortObject, DataStringCheck } from '../../../Services/utils/formators'
import { ErrorHandler } from '../../../Store/Firebase/Utils'

import FirebaseSection from '../../../Store/Firebase/Section'
import FirebasePen from '../../../Store/Firebase/Pen'

const DataEditor = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)
  
  const farmId = useSelector(state => state.firebase.activeData&&state.firebase.activeData.farm?state.firebase.activeData.farm:undefined)
  const activeData = useSelector(state => state.firebase.activeData?state.firebase.activeData:undefined)
  const sectionDatas = useSelector(state => state.firebase.sections)
  
  //Pens, 
  const DataTypes = ["Pens"]
  const [SelectedDataType, setSelectedDataType] = useState(DataTypes[0])

  //Add, Remove
  const ActionsTypes = ["Add", "Remove"]
  const [SelectedActionsType, setSelectedActionsType] = useState(ActionsTypes[0])

  const [Sections, setSections] = useState({})
  const [Pens, setPens] = useState({})
  const [SelectedSection, setSelectedSection] = useState("")
  const [SelectedPen, setSelectedPen] = useState("")

  const [PrePen, setPrePen] = useState("Pen_")
  const [PostPen, setPostPen] = useState("")
  const [Lowest, setLowest] = useState(1)
  const [Higest, setHigest] = useState(2)
  const [isMultiPen, setisMultiPen] = useState(false)
  const [NewSection, setNewSection] = useState(true)
  
  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (farmId) {
      dispatch(FirebaseSection.action({FarmId: farmId}))
    }
  },[farmId])

  useEffect(() => {
    var _Sections = sectionDatas?sortObject(sectionDatas):{}
    var Section = ""
    if (_Sections) {
      Section = Object.keys(_Sections)[0]
      setNewSection(false)
    } else setNewSection(true)

    if (activeData && activeData.section && _Sections[activeData.section]){
      Section = activeData.section
    }
    setSections(_Sections)
    if (SelectedSection && SelectedSection === Section){
      Pens_Setter(_Sections)
    } else setSelectedSection(Section)
  },[sectionDatas, activeData])

  const Pens_Setter = (_Sections) => {
    var _Pens = _Sections&&_Sections[SelectedSection]?sortObject(_Sections[SelectedSection].pens):{}
    var Pen = _Pens&&Object.keys(_Pens).length>0?Object.keys(_Pens)[0]:""
    if (activeData && activeData.pen && _Pens[activeData.pen]){
      Pen = activeData.pen
    }

    setPens(_Pens)
    setSelectedPen(Pen)
  }

  useEffect(() => {
    Pens_Setter(Sections)
  },[SelectedSection])

  return(
    <SafeAreaView style={[Layout.fill, Layout.fullSize, Common.backgroundPrimary]}>
      {firebaseError && firebaseError.message && (
        <Text style={[{color:Colors.error}]}>{firebaseError.message}</Text>
      )}
      <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
        <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
          <View style={[Layout.midWidth, Layout.row, {justifyContent:'space-between', marginTop:10, marginBottom:10, minWidth:330, maxWidth:660}]}>
            <Picker
              enabled={false}
              mode="dropdown"
              selectedValue={SelectedActionsType}
              style={ [{width:'45%', height: 50 }]}
              onValueChange={(itemValue, itemIndex) => {
                setSelectedDataType(itemValue)
              }}
            >
              {DataTypes.map((DataType, i) => {
                return (<Picker.Item label={DataType} value={DataType} key={i}/>)
              })}
            </Picker>
            <Picker
              enabled={true}
              mode="dropdown"
              selectedValue={SelectedActionsType}
              style={ [{width:'45%', height: 50, alignSelf:'flex-end' }]}
              onValueChange={(itemValue, itemIndex) => {
                setSelectedActionsType(itemValue)
              }}
            >
              {ActionsTypes.map((Action, i) => {
                return (<Picker.Item label={Action} value={Action} key={i}/>)
              })}
            </Picker>
          </View>
          <View style={[Layout.fill, Layout.midWidth, Common.border, {padding:5, minWidth:330, maxWidth:660}]}>
            <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between', marginBottom:4}]}>
              <View style={[Layout.column, Layout.halvWidth]}>
                <Section_Picker Actions={ActionsTypes} Action={SelectedActionsType} NewSection={NewSection} Sections={Sections} Selected={SelectedSection} onSelected={({New, Selected}) =>{
                  if (New) {
                    setNewSection(true)
                  }
                  setSelectedSection(Selected)
                }}/>

              </View>
              <View style={[Layout.column, Layout.halvWidth, {alignItems:'flex-end'}]}>
                {SelectedActionsType===ActionsTypes[0]&&(
                  <View style={[Layout.row, Layout.fullWidth, {borderWidth:0, height:50, alignItems:'center', justifyContent:'flex-end'}]}>
                    <Text style={[{marginLeft: 5}]}>Multiple: </Text>
                    <CheckBox style={[{marginTop:2, marginHorizontal:5}]}
                      value={isMultiPen}
                      onValueChange={() => {
                        setisMultiPen(old => !old)
                      }}
                    />
                  </View>
                )}
              </View>
            </View>

            <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between'}]}>
              <View style={[Layout.fillCol, Layout.fullWidth]}>
                <Pen_Picker Actions={ActionsTypes} Action={SelectedActionsType} IsMultiple={isMultiPen} Lowest={Lowest} Higest={Higest} Pens={Pens} PrePen={PrePen} PostPen={PostPen} Selected={SelectedPen} onSelected={(Res) => {
                  if (Res.Selected) setSelectedPen(Res.Selected)
                  if (isMultiPen) {
                    if (Res.Lowest) setLowest(Res.Lowest)
                    if (Res.Higest) setHigest(Res.Higest)
                    if (Res.PrePen) setPrePen(Res.PrePen)
                    if (Res.PostPen) setPostPen(Res.PostPen)
                  }
                }}/>
              </View>
            </View>
          </View>
          <View style={[Layout.midWidth, {alignItems:'center', marginTop:10}]}>
            <Button Texts={["Submit"]}
              Actions={[() => {
                if (!SelectedSection || SelectedSection === "") {
                  dispatch(ErrorHandler.action({message: "A section is needed"}))
                } else if ((!isMultiPen && (!SelectedPen || SelectedPen === "")) ||
                           (isMultiPen && ((!PrePen || PrePen === "") && (!PostPen || PostPen === "")))) {
                  dispatch(ErrorHandler.action({message: "A pen name is needed"}))
                } else if (!isNaN(SelectedSection) || !isNaN(SelectedSection[0])) {
                  dispatch(ErrorHandler.action({message: "Section name cannot be or start with a number"}))
                } else if (!isMultiPen && (!isNaN(SelectedPen) || !isNaN(SelectedPen[0])) ||
                           (isMultiPen && (!isNaN(PrePen) || !isNaN(PrePen[0])))) {
                  dispatch(ErrorHandler.action({message: "Pen name cannot be or start with a number"}))
                } else if (isMultiPen && (Lowest === "" || Higest === "")) {
                  dispatch(ErrorHandler.action({message: "Lowest and Higest have to be a valid number"}))
                } else if (isMultiPen && (parseInt(Lowest) > parseInt(Higest)) ) {
                  dispatch(ErrorHandler.action({message: "Higest have to be a higher value than Lowest"}))
                }
                else {
                  var _Lowest = 1, _Higest = 1
                  if (isMultiPen) {
                    _Lowest = parseInt(Lowest)
                    _Higest = parseInt(Higest)
                  }
                  console.log(Lowest, Higest)
                  for (var index = Lowest; index <= _Higest; index++) {
                    var Pen = isMultiPen?(PrePen + index + PostPen):SelectedPen
                    var exists = false;

                    Object.keys(Sections).map((section, i) => {
                      if (Sections[section].pens){
                        var test = Object.keys(Sections[section].pens).filter(e => e === Pen).length
                        if (test) exists = true
                      }
                    })
                    
                    if (exists && SelectedActionsType === ActionsTypes[0]) {
                      dispatch(ErrorHandler.action({message: "Given pen name already exists"}))
                    } else if (!exists && SelectedActionsType === ActionsTypes[1]) {
                      dispatch(ErrorHandler.action({message: "Cannot remove non-existing pen"}))
                    }
                    else {
                      if (SelectedActionsType === ActionsTypes[0]) {
                        dispatch(FirebasePen.create({FarmId:farmId, PenId:Pen, SectionId:SelectedSection})).then(data => {
                          if (data.meta.requestStatus === "fulfilled") { // secound hand reload Sections
                            dispatch(FirebaseSection.action({FarmId: farmId}))
                          }
                        })
                      } else if (SelectedActionsType === ActionsTypes[1]) {
                        dispatch(FirebasePen._delete({FarmId:farmId, PenId:Pen, SectionId:SelectedSection})).then((data)=>{
                          if (data.meta.requestStatus === "fulfilled") { // secound hand reload Sections
                            dispatch(FirebaseSection.action({FarmId: farmId}))
                          }
                        })
                      }
                    }
                  }
                }
              }]}
            />
          </View>
        </View>
      </ScrollView>
    </SafeAreaView>
  )
}

export default DataEditor

const Section_Picker = ({Actions, Action, NewSection, Sections, Selected, onSelected}) => {
  const { t } = useTranslation()
  const { Layout, Common } = useTheme()

  if (NewSection && Action === Actions[0]) {
    return(
      <Input
        label="Section Name"
        onChangeText={text => {
          if (text){
            if (DataStringCheck(text)) {
              onSelected(text)
            }
          }
        }}
        keyboardType={'default'}
        value={Selected}
        selectTextOnFocus
        style={[Layout.fill, Common.textInput]}
      />
    )
  } else {
    var _Sections = Sections
    if (Action === Actions[0]) {
      _Sections["New Section"] = {}
    }

    return(
      <Picker
        mode="dropdown"
        selectedValue={Selected}
        style={ [Layout.midWidth, { height: 50, }]}
        onValueChange={(itemValue, itemIndex) => {
          if (itemValue === "New Section") {
            onSelected({New:true, Selected:"Section_"+Object.keys(Sections).length})
          } else {
            onSelected({New:false, Selected: itemValue})
          }
        }}
      >
        {Object.keys(_Sections).map((Section, i) => {
            return (<Picker.Item label={Section} value={Section} key={i}/>)
        })}
      </Picker>
    )
  }
}

const Pen_Picker = ({Actions, Action, IsMultiple, Lowest, Higest, PrePen, PostPen, Pens, Selected, onSelected}) => {
  const { t } = useTranslation()
  const { Layout, Common } = useTheme()

  if (Action === Actions[0]) {
    if (IsMultiple) {
      return(
        <View>
          <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between', marginBottom:4}]}>
            <Input
              label="Lowest number"
              //containerStyle={[{width:'40%', borderWidth:1, marginTop:10}]}
              //style={[ApplicationStyles.input, {width:'40%', marginBottom:-10}]}
              onChangeText={text => {
                if ((text && !isNaN(text) && text !== "0") || text === "") {
                  onSelected({Lowest: text})
                }
              }}
              keyboardType={'default'}
              value={Lowest}
              selectTextOnFocus
              style={[Layout.fill, Common.textInput, {width:100}]}
              containerStyle={[{width:'40%'}]}
            />
            <Text style={[{alignSelf:'center', width:'20%', textAlign:'center'}]}>-</Text>
            <Input
              label="Higest number"
              //containerStyle={[{width:'40%', borderWidth:1, marginTop:10}]}
              //style={[ApplicationStyles.input, {width:'40%', marginBottom:-10}]}
              onChangeText={text => {
                if ((text && !isNaN(text) && text !== "0") || text === "") {
                  onSelected({Higest: text})
                }
              }}
              keyboardType={'default'}
              value={Higest}
              selectTextOnFocus
              style={[Layout.fill, Common.textInput, {width:100}]}
              containerStyle={[{width:'40%'}]}
            />
          </View>
          <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between', marginBottom:4}]}>
            <Input
              label="Pre Pen name"
              //containerStyle={[{width:'40%', borderWidth:1, marginTop:10}]}
              //style={[ApplicationStyles.input, {width:'40%', marginBottom:-10}]}
              onChangeText={text => {
                if (text){
                  if (DataStringCheck(text)){
                    onSelected({PrePen: text})
                  }
                }
              }}
              keyboardType={'default'}
              value={PrePen}
              selectTextOnFocus
              style={[Layout.fill, Common.textInput, {width:100}]}
              containerStyle={[{width:'40%'}]}
            />
            <Text style={[{alignSelf:'center', width:'20%', textAlign:'center'}]}>[{Lowest}-{Higest}]</Text>
            <Input
              label="Post Pen name"
              //containerStyle={[{width:'40%', borderWidth:1, marginTop:10}]}
              //style={[ApplicationStyles.input, {width:'40%', marginBottom:-10}]}
              onChangeText={text => {
                if (text){
                  if (DataStringCheck(text)){
                    onSelected({PostPen: text})
                  }
                }
              }}
              keyboardType={'default'}
              value={PostPen}
              selectTextOnFocus
              style={[Layout.fill, Common.textInput, {width:100}]}
              containerStyle={[{width:'40%'}]}
            />
          </View>
        </View>
      )
    }else {
      return (
        <View style={[Layout.halvWidth]}>
          <Input
            label="Pen name"
            onChangeText={text => {
              if (text){
                if (DataStringCheck(text)) {
                  onSelected({Selected:text})
                }
              }
            }}
            keyboardType={'default'}
            value={Selected}
            selectTextOnFocus
            style={[Layout.fill, Common.textInput, Layout.midWidth]}
          />
        </View>
      )
    }
  }
  else if (Action === Actions[1]) {
    return(
      <View style={[Layout.halvWidth]}>
        <Picker
          mode="dropdown"
          selectedValue={Selected}
          style={ [Layout.midWidth, { height: 50 }]}
          onValueChange={(itemValue, itemIndex) => {
            if (Pens[itemValue]) {
              onSelected({Selected:itemValue})
            }
          }}
        >
          {Object.keys(Pens).map((Pen, i) => {
            return (<Picker.Item label={Pen} value={Pen} key={i}/>)
          })}
        </Picker>
      </View>
    )
  }else return null
}