import React, { useEffect, useState } from 'react'
import { createStackNavigator } from '@react-navigation/stack'
import { IndexStartupContainer } from '../Containers'
import { useSelector } from 'react-redux'
import { NavigationContainer } from '@react-navigation/native'
import { navigationRef } from '../Navigators/Root'
import { SafeAreaView, StatusBar } from 'react-native'
import { useTheme } from '../Theme'
const Stack = createStackNavigator()

let MainNavigator
let AuthNavigator
let AdminNavigator
let UserNavigator

// @refresh reset
const ApplicationNavigator = () => {
  const { Layout, darkMode, NavigationTheme } = useTheme()
  const { colors } = NavigationTheme
  const [isApplicationLoaded, setIsApplicationLoaded] = useState(false)
  const applicationIsLoading = useSelector(state => state.startup.loading)

  useEffect(() => {
    if (MainNavigator == null && !applicationIsLoading) {
      MainNavigator = require('../Navigators/Main').default
    }
    if (AuthNavigator == null && !applicationIsLoading) {
      AuthNavigator = require('../Navigators/Auth').default
    }
    if (AdminNavigator == null && !applicationIsLoading) {
      AdminNavigator = require('../Navigators/Admin').default
    }
    if (UserNavigator == null && !applicationIsLoading) {
      UserNavigator = require('../Navigators/User').default
    }
    if (MainNavigator !== null && AuthNavigator !== null && AdminNavigator !== null && UserNavigator !== null) {
      setIsApplicationLoaded(true)
    }
  }, [applicationIsLoading])

  // on destroy needed to be able to reset when app close in background (Android)
  useEffect(
    () => () => {
      setIsApplicationLoaded(false)
      MainNavigator = null
      AuthNavigator = null
    },
    [],
  )

  return (
    <SafeAreaView style={[Layout.fill, { backgroundColor: colors.card }]}>
      <NavigationContainer theme={NavigationTheme} ref={navigationRef}>
        <StatusBar barStyle={darkMode ? 'light-content' : 'dark-content'} />
        <Stack.Navigator headerMode={'none'} initialRouteName={"Startup"}>
          <Stack.Screen name="Startup" component={IndexStartupContainer} />
          {isApplicationLoaded && MainNavigator != null && (
            <Stack.Screen
              name="Main"
              component={MainNavigator}
              options={{
                animationEnabled: false,
              }}
            />
          )}
          {isApplicationLoaded && AuthNavigator != null && (
            <Stack.Screen
              name="Auth"
              component={AuthNavigator}
              options={{
                animationEnabled: false,
              }}
            />
          )}
          {isApplicationLoaded && AdminNavigator != null && (
            <Stack.Screen
              name="Admin"
              component={AdminNavigator}
              options={{
                animationEnabled: false,
              }}
            />
          )}
          {isApplicationLoaded && UserNavigator != null && (
            <Stack.Screen
              name="User"
              component={UserNavigator}
              options={{
                animationEnabled: false,
              }}
            />
          )}
        </Stack.Navigator>
      </NavigationContainer>
    </SafeAreaView>
  )
}

export default ApplicationNavigator
