import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { View, Text, Picker, StyleSheet } from 'react-native'
import { Input } from 'react-native-elements'
import { useTheme } from '../Theme'
import { useTranslation } from 'react-i18next'
import { Button, Datetime_Picker } from './index'
import { navigationRef, isApp } from '../Navigators/Root'

const Comment_Module = ({ maxWidth, Title, HaveDate, HaveCodeList, HaveQuantity, HaveWeight, onDone }) => {
  const { t } = useTranslation()
  const { Layout, Common, Fonts, Colors } = useTheme()

  const [InputText, setInputText] = useState("");
  const [QuantityText, setQuantityText] = useState("1");
  const [WeightText, setWeightText] = useState("");
  const [CodeListItem, setCodeListItem] = useState(0);
  const [DateSet, setDateSet] = useState(new Date(Date.now()));
  return (
    <View style={[Common.border, Layout.fullWidth, { maxWidth: maxWidth, paddingVertical:10, paddingHorizontal:5}]}>
      <View style={styles.textAreaContainer} >
        <View style={isApp()?[{}]:[Layout.row,{justifyContent:'space-between'}]}>
          {((Title&&Title !=="")||HaveDate||HaveCodeList)&&(
            <View style={[Layout.column, {marginBottom:5}]}>
              {(Title&&Title !=="")&&(<Text style={[Fonts.textRegular, {height: 48, color:Colors.input}]}>{Title}</Text>)}
              {HaveDate&&(
                <View style={[Layout.fullWidth, {alignItems:'flex-start', marginBottom:20}]}>
                  {<Datetime_Picker Selected={DateSet}
                      onValueChange={(date)=>{
                      setDateSet(date)
                    }}/>}
                </View>
              )}
              {HaveCodeList&&(
                <Picker
                  mode="dropdown"
                  selectedValue={CodeListItem}
                  style={[Layout.fullWidth, {height: 30}]}
                  onValueChange={(itemValue, itemIndex) => {
                    setCodeListItem(itemValue)
                  }}
                >
                  <Picker.Item label={"---"} value={0} />
                  <Picker.Item label={"Relocation"} value={1} />
                  <Picker.Item label={"Hernia"} value={2} />
                  <Picker.Item label={"Diarrhea"} value={3} />
                </Picker>
              )}
            </View>
          )}
          {(HaveQuantity||HaveWeight)&&(
            <View style={[Layout.column, {marginBottom:5}]}>
              {HaveQuantity&&(
                <View style={[Layout.rowCenter, {marginBottom:5}]}>
                  <Text style={[Fonts.normal, {width:75, color:Colors.input, textAlignVertical:'center'}]}>Quantity:</Text>
                  <Input style={[styles.textArea, {minHeight:35,height:35, width:55, paddingBottom:-10}]}
                    containerStyle={[{minHeight:35,height:35, width:55, paddingHorizontal:0}]}
                    inputStyle={{paddingHorizontal:5}}
                    multiline={false}
                    onChangeText={(text) => {
                      if (text === "" || !isNaN(text)) setQuantityText(text)
                    }}
                    value={QuantityText}
                  />
                </View>
              )}
              {HaveWeight&&(
                <View style={[Layout.rowCenter]}>
                  <Text style={[Fonts.normal, {width:75, color:Colors.input, textAlignVertical:'center'}]}>Average Weight:</Text>
                  <Input style={[styles.textArea, {minHeight:35, height:35, width:55, paddingBottom:-10}]} 
                    containerStyle={[{minHeight:35,height:35, width:55, paddingHorizontal:0}]}
                    inputStyle={{paddingHorizontal:5}}
                    multiline={false}
                    onChangeText={(text) => {
                      if (text === "" || !isNaN(text)) setWeightText(text)
                    }}
                    value={WeightText}
                  />
                </View>
              )}
            </View>
          )}
        </View>
        <Input
          style={[styles.textAreaContainer, styles.textArea]}
          underlineColorAndroid='transparent'
          placeholder={t('CommentModule.NotePlaceholder')}
          placeholderTextColor="gray"
          numberOfLines={10}
          multiline={true}
          onChangeText={(text) => setInputText(text)}
          value={InputText}
        />
      </View>
      <Button Texts={[t('ActionButtons.Done'),t('ActionButtons.Cancel')]} Icons={[]} Actions={[ele => {
        if (onDone) {
          if (ele.index === 1) onDone({res:false, data:InputText})
          else {
            onDone({res: true, data: InputText, quantity: QuantityText, weight: WeightText, code:CodeListItem, date:DateSet.getTime()})
          }
        }
      }]} />
    </View>
  )
}

const styles = StyleSheet.create({
  textAreaContainer: {
    borderColor: '#a6a6a6',
    borderWidth: 1,
    padding: 5,
    marginBottom:10
  },
  textArea: {
    height: 150,
    justifyContent: "flex-start",
    borderColor: "black",
    borderWidth: 0.5,
  }
})

Comment_Module.propTypes = {
  maxWidth: PropTypes.number,
  Title: PropTypes.string,
  HaveDate: PropTypes.bool,
  HaveQuantity: PropTypes.bool,
  HaveWeight: PropTypes.bool,
  HaveCodeList: PropTypes.bool,
}

Comment_Module.defaultProps = {
  maxWidth: 600,
  Title: "",
  HaveDate: false,
  HaveQuantity: false,
  HaveWeight: false,
  HaveCodeList: false,
}

export default Comment_Module
