import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  ActivityIndicator,
  Text,
  TouchableOpacity,
  Button,
} from 'react-native'
import { Brand, Frame } from '../../../Components'
import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef } from '../../../Navigators/Root'

import FirebaseUser from '../../../Store/Firebase/User'
import { ErrorHandler } from '../../../Store/Firebase/Utils'
import { UserStates } from '../../../Services/utils/defines'
import { auth } from '../../../Services/Firebase/Initial'
import LoginContainer from './UserLogin'

const PanelsSorter = (userType) => {
  switch(userType) {
    case UserStates.ADMIN:
    case UserStates.TECH:
      return "AdminPanel"
    case UserStates.OWNER:
      return "ConsysPanel"
    case UserStates.FREE:
      return "UserPanel"
    case UserStates.STARTER:
      return "CreateUserData"
    default:
      return "LoginUser"
  }
};

const IndexAuthContainer = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout } = useTheme()

  const [isAuthLoaded, setIsAuthLoaded] = useState(false)

  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const [isUserLoaded, setIsUserLoaded] = useState(false)
  const User = useSelector(state => state.firebase.user)

  const logout = () => {
    dispatch(FirebaseUser.logout())
  }

  //User site check
  useEffect(() => {
    if (!User || !User.state) setIsUserLoaded('Login')
    else {
      if (User.state === UserStates.STARTER && !User.emailVerified) {
        setIsUserLoaded('Verify')
      } else {
        switch(User.state) {
          case UserStates.ADMIN:
          case UserStates.TECH:
            navigationRef.current?.navigate('Admin', {screen: 'Home'})
            break;
          case UserStates.OWNER:
            navigationRef.current?.navigate('User', {screen: 'Home'}) // mabye special ?? not implamentet
            break;
          case UserStates.FREE:
            navigationRef.current?.navigate('User', {screen: 'Home'})
            break;
          case UserStates.STARTER:
            navigationRef.current?.navigate('Auth', {screen: 'CreateUser'})
            break;
          default:
            setIsUserLoaded('Login')
            break;
        }
      }
    }
  }, [User])

  useEffect(() => {
    dispatch(ErrorHandler.action())
  },[])
  
  if (!isUserLoaded) {
    return (
      <View style={[Layout.fill, Layout.colCenter, Common.backgroundPrimary]}>
        <Brand />
        <ActivityIndicator size={'large'} style={[Gutters.largeVMargin]} />
        <Text style={Fonts.textCenter}>{t('SplashText')}</Text>
      </View>
    )
  } else if (isUserLoaded === "Login") {
    return (
      <LoginContainer/>
    )
  } else if (isUserLoaded === "Verify") {
    return (
      <View style={[Layout.fill, Layout.colCenter]}>
        <Text>Do Verify ???</Text>
      </View>
    )
  }
}

export default IndexAuthContainer
