import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update, updateData, create} from '../../Services/Firebase/Batchs'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/batchs', fetch),
  update: buildAsyncActions('firebase/batchs', update),
  create: buildAsyncActions('firebase/batchs', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'batchs',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}