import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  FlatList,
  ScrollView,
  Picker,
} from 'react-native'
import { Input } from 'react-native-elements'
import { LoadingSplash, Brand, Frame, Button, ContentProp, Datetime_Picker } from '../../../Components'

import SelectBox from 'react-native-multi-selectbox'
import { xorBy } from 'lodash'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { ErrorHandler } from '../../../Store/Firebase/Utils'
import { DataStates } from '../../../Services/utils/defines'
import { DataStringCheck, sortObject } from '../../../Services/utils/formators'
import FirebaseBatch from '../../../Store/Firebase/Batch'
import FirebaseGraph from '../../../Store/Firebase/Graph'

import FirebaseSection from '../../../Store/Firebase/Section'
import FirebasePen from '../../../Store/Firebase/Pen'

const Batchs = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const farmId = useSelector(state => state.firebase.activeData&&state.firebase.activeData.farm?state.firebase.activeData.farm:undefined)
  const batchDatas = useSelector(state => state.firebase.batchs)
  const graphDatas = useSelector(state => state.firebase.graphs)  
  const sectionDatas = useSelector(state => state.firebase.sections)
  const pensDatas = useSelector(state => state.firebase.pens)

  const [EditView, setEditView] = useState(0)
  const [HideClosed, setHideClosed] = useState(true)
  const [SelectedBatch, setSelectedBatch] = useState("")

  const [StartDate, setStartDate] = useState(new Date(Date.now()))
  const [SelectedCurve, setSelectedCurve] = useState("")
  const [TargetWeight, setTargetWeight] = useState(33)
  const [DevianceMin, setDevianceMin] = useState(2)
  const [DevianceMax, setDevianceMax] = useState(2)

  const [freePens, setfreePens] = useState([])
  const [avaiblePens, setavaiblePens] = useState([])
  const [selectedPens, setselectedPens] = useState([])

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  useEffect(() => {
    if (batchDatas && sectionDatas){
      console.log("fetch batch")
      var _freePens = []
      Object.keys(sectionDatas).forEach(section => {
        if (sectionDatas[section].pens) {
          var pens = []
          Object.keys(sectionDatas[section].pens).filter(e => e !== 'update_time').forEach(pen => {
            var add = true
            var batchList = Object.keys(batchDatas).filter(e => e !== 'update_time' && (!batchDatas[e].state || batchDatas[e].state !== DataStates.CLOSED))
            batchList.forEach(batch => {
              if (batchDatas[batch].pens && batchDatas[batch].pens[pen] && !batchDatas[batch].pens[pen].enddate) {
                add = false;
              }
            });
            if (add) {
              var item = {
                item: pen,
                id: section + "_" + pen
              }
              _freePens.push(item)
            }
          })
        }
      });
      setfreePens(_freePens)
      setavaiblePens(_freePens)
    }
  }, [batchDatas, sectionDatas])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (farmId) {
      dispatch(FirebaseBatch.action({FarmId: farmId}))
      dispatch(FirebaseGraph.action({FarmId: farmId}))
      dispatch(FirebaseSection.action({FarmId: farmId}))
      dispatch(FirebasePen.action({FarmId: farmId}))
    }
  },[farmId])

  const onMultiChange = (item) => {
    var items = xorBy(selectedPens, [item], 'id')
    setselectedPens(items)
  }

  if (batchDatas && typeof batchDatas !== 'undefined') {
    return(
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Common.backgroundPrimary]}>
        {firebaseError && firebaseError.message && (
          <Text style={[{color:Colors.error}]}>{firebaseError.message}</Text>
        )}
        <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
          {EditView === 0?(
            <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
              <Button Texts={[t('Batchs.New'), t('Batchs.Edit')]} Frame={true} Actions={[(ele) => {
                if (ele.index === 0) {
                  var graph = 0
                  if (graphDatas) {
                    graph = Object.keys(graphDatas).filter(e => e !== 'update_time')[0]
                  }
                  
                  setSelectedBatch("")
                  setSelectedCurve(graph)
                  setTargetWeight(33)
                  setDevianceMin(2)
                  setDevianceMax(2)

                  setavaiblePens(freePens)
                  setselectedPens([])

                  setEditView(1)
                }else {
                  if (batchDatas) {
                    var batchList = Object.keys(batchDatas).filter(e => e !== 'update_time' && (!batchDatas[e].state || batchDatas[e].state !== DataStates.CLOSED))
                    if (batchList.length > 0) {
                      var batchName = batchList[0]
                      var batch = batchDatas[batchName]
                      if (batch) {
                        var Pens = []
                        var pens = sortObject(batch.pens)
                        Object.keys(pens).forEach(element => {
                          if (!batch.pens[element].enddate) {
                            if (pensDatas && pensDatas[element]){
                              var _pen = pensDatas[element]
                              var item = {}
                              item.item = element;
                              item.id = _pen.section + "_" + element;
                              Pens.push(item)
                            }
                          }
                        });
                        var target = batch.datas&&batch.datas.target?batch.datas.target:33

                        setSelectedBatch(batchName)
                        setStartDate(new Date(batch.time))
                        setSelectedCurve(batch.gcurve)
                        setTargetWeight(target)
                        setDevianceMin(batch.datas.devianceMin)
                        setDevianceMax(batch.datas.devianceMax)
                        setavaiblePens(freePens.concat(Pens))
                        setselectedPens(Pens)

                        setEditView(2)
                      }
                    }
                  }
                }
              }]} />
              <View style={[Layout.fullWidth, Layout.colVCenter, {marginVertical:20, marginBottom:40}]}>
                <Button Texts={Object.keys(batchDatas).filter(e => e !== 'update_time' && (!batchDatas[e].state || batchDatas[e].state !== DataStates.CLOSED))} Actions={[(ele) => {
                  setSelectedBatch(ele.text)
                  //setEditView(2)
                }]}/>
              </View>
              <Button Texts={[!HideClosed?t('Batchs.Hide'):t('Batchs.Show')]} Actions={[() => {
                setHideClosed(val => !val)
              }]} />
              {!HideClosed&&(
                <View style={[Layout.fullWidth, Layout.colVCenter, {marginVertical:20}]}>
                  <Button Texts={Object.keys(batchDatas).filter(e => e !== 'update_time' && (batchDatas[e].state && batchDatas[e].state === DataStates.CLOSED))} Actions={[(ele) => {
                    setSelectedBatch(ele.text)
                    //setEditView(2)
                  }]}/>
                </View>
              )}
            </View>
          ):(
            <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
              {EditView === 1 &&( // Create
                <Input label={t('Batchs.Name')}
                  containerStyle={[Layout.midWidth, {borderWidth:1, marginTop:10}]}
                  style={[Layout.fill, Layout.midWidth, Common.textInput]}
                  onChangeText={text => {
                    if (text){
                      if (DataStringCheck(text)){
                        setSelectedBatch(text)
                      }
                    }else {
                      setSelectedBatch(text)
                    }
                  }}
                  value={
                    SelectedBatch
                  }
                  selectTextOnFocus
                />
              )}
              {EditView === 2&&( // Edit
                <Picker
                  mode="dropdown"
                  selectedValue={SelectedBatch}
                  style={[Layout.halvWidth, { height: 50, justifyContent:"flex-end"}]}
                  onValueChange={(itemValue, itemIndex) => {
                    if (batchDatas && batchDatas[itemValue]) {
                      var batch = batchDatas[itemValue]

                      var Pens = []
                      var pens = sortObject(batch.pens)
                      Object.keys(pens).forEach(element => {
                        if (!batch.pens[element].enddate) {
                          if (pensDatas && pensDatas[element]){
                            var _pen = pensDatas[element]
                            var item = {}
                            item.item = element;
                            item.id = _pen.section + "_" + element;
                            Pens.push(item)
                          }
                        }
                      });
                      var target = batch.datas&&batch.datas.target?batch.datas.target:33

                      setSelectedBatch(itemValue)
                      setStartDate(new Date(batch.time))
                      setSelectedCurve(batch.gcurve)
                      setTargetWeight(target)
                      setDevianceMin(batch.datas.devianceMin)
                      setDevianceMax(batch.datas.devianceMax)
                      setavaiblePens(freePens.concat(Pens))
                      setselectedPens(Pens)
                    }
                  }}
                >
                  {Object.keys(batchDatas).filter(e => e !== 'update_time' && (!batchDatas[e].state || batchDatas[e].state !== DataStates.CLOSED)).map((batch, i) => {
                    return (<Picker.Item label={batch} value={batch} key={i}/>)
                  })}
                </Picker>
              )}
              <View style={[Layout.fullWidth, Layout.alignItemsCenter, {marginVertical:20}]}>
                <View style={[Layout.midWidth, isApp()?{}:Layout.row, Layout.justifyContentCenter, {minWidth:330}]}>
                  <View style={[isApp()?{}:Layout.midWidth, {minWidth:315, borderWidth:1 , marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center", zIndex:1, backgroundColor:Colors.inputBackground}]}>
                    <View style={[Layout.fullWidth, {alignItems:"flex-start", height:22}]}>
                      <Text style={[Layout.fullWidth, Common.textInput, {marginHorizontal:5, marginVertical:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>{t('Batchs.StartD')}</Text>
                    </View>
                    <View style={[Layout.fullWidth, isApp()?{marginBottom:10}:{}, {alignItems:'flex-start', height:28}]}>
                      <Datetime_Picker Selected={StartDate}
                        onValueChange={(date)=>{
                          setStartDate(date)
                        }}
                      />
                    </View>
                  </View>
                  <View style={[isApp()?{}:Layout.quarterWidth, {minWidth:120,borderWidth:1, marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center", zIndex:1, backgroundColor:Colors.inputBackground}]}>
                    <View style={[Layout.fullWidth, {alignItems:"flex-start", height:22}]}>
                      <Text style={[Layout.fullWidth, Common.textInput, {marginHorizontal:5, marginVertical:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>{t('Batchs.GrowthCurve')}</Text>
                    </View>
                    <View style={[Layout.fullWidth, isApp()?{marginBottom:10}:{}, {alignItems:"center", height:28}]}>
                      <Picker
                        mode="dropdown"
                        selectedValue={SelectedCurve}
                        style={[Layout.fullWidth, {height: 30}]}
                        onValueChange={(itemValue, itemIndex) => {
                          setSelectedCurve(itemValue)
                        }}
                      >
                        {Object.keys(graphDatas).filter(e => e !== 'update_time').map((graph, i) => {
                          return (<Picker.Item label={graph} value={graph} key={i}/>)
                        })}
                      </Picker>
                    </View>
                  </View>
                </View>
                <View style={[Layout.midWidth, isApp()?{}:Layout.row, Layout.justifyContentCenter, {minWidth:330}]}>
                  <Input label={t('Batchs.Target')}
                      type='number'
                      step="0.1"

                      containerStyle={[Layout.halvWidth,{borderWidth:1, marginTop:10}]}
                      style={[Common.textInput, {marginBottom:-10, width:100}]}
                      onChangeText={text => {
                        if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                          setTargetWeight(text)
                        }
                      }}
                      value={
                        TargetWeight
                      }
                    />
                    <View style={[Layout.halvWidth]} />
                </View>
                <View style={[Layout.midWidth, Layout.row, Layout.justifyContentCenter, {minWidth:330}]}>
                  <Input label={t('Batchs.DevianceMin')}
                    type='number'
                    step="0.1"

                    containerStyle={[Layout.halvWidth,{borderWidth:1, marginTop:10}]}
                    style={[Common.textInput, {marginBottom:-10, width:100}]}
                    onChangeText={text => {
                      if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                        setDevianceMin(text)
                      }
                    }}
                    value={
                      DevianceMin
                    }
                  />
                  <Input label={t('Batchs.DevianceMax')}
                    type='number'
                    step="0.1"

                    containerStyle={[Layout.halvWidth,{borderWidth:1, marginTop:10}]}
                    style={[Common.textInput, {marginBottom:-10, width:100}]}
                    onChangeText={text => {
                      if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                        setDevianceMax(text)
                      }
                    }}
                    value={
                      DevianceMax
                    }
                  />
                </View>
                <View style={[Layout.midWidth, isApp()?{}:Layout.row, Layout.justifyContentCenter, {minWidth:330}]}>
                  <View style={[Layout.fullWidth, {borderWidth:1, marginTop:10, paddingHorizontal:10, paddingTop:5}]}>
                    <SelectBox
                      label={t('Batchs.PensIn')}
                      labelStyle={{color:Colors.input, fontSize: 15, fontWeight: "bold"}}
                      optionsLabelStyle={{minWidth:100, color:"black", fontSize: 12, fontWeight: "bold"}}

                      options={avaiblePens}
                      selectedValues={selectedPens}
                      onMultiSelect={onMultiChange}
                      onTapClose={onMultiChange}
                      isMulti
                    />
                  </View>
                </View>
                {EditView === 2&&(
                  <View style={[Layout.fullWidth, Layout.alignItemsCenter, {marginVertical:10}]}>
                    <Button Texts={[t('Batchs.Close')]} Actions={[ele => {
                      var Batch = batchDatas[SelectedBatch];
                      if (Batch) {
                        var Error = false
                        if (Batch.pens) {
                          Object.keys(Batch.pens).forEach(PenId => {
                            if (!Error && pensDatas[PenId] && pensDatas[PenId].datas && pensDatas[PenId].datas.count && pensDatas[PenId].datas.count > 0){
                              Error = "The batch still contains active pens, please clear the pens before closing the batch"
                            }
                          });
                        }
                        if (Error) dispatch(ErrorHandler.action({message: Error}))
                        else {
                          dispatch(FirebaseBatch.update({FarmId:farmId, BatchId:SelectedBatch, Data: {state: DataStates.CLOSED}})).then(data => {
                            if (data.meta.requestStatus === "fulfilled") { // secound hand reload Sections
                              dispatch(FirebasePen.action({FarmId: farmId}))
                              setEditView(0)
                            }
                          })
                        }
                      }
                    }]} />
                  </View>
                )}
                <Button Texts={[t('ActionButtons.Save'),t('ActionButtons.Cancel')]} Frame={true} Actions={[ele => {
                  if (ele.index === 1) {
                    setEditView(0)
                  }
                  else { //Save new / edit - batch
                    var update = true;
                    if (SelectedBatch && SelectedBatch !== "") {
                      if (batchDatas[SelectedBatch]) {
                        if (EditView === 1) {
                          dispatch(ErrorHandler.action({message: "BatchName error, batch allready exists"}))
                          update = false 
                        }
                      } else {
                        if (EditView ===2 ) {
                          dispatch(ErrorHandler.action({message: "BatchName error, batch does not exists"}))
                          update = false 
                        }
                      }
                    } else {
                      dispatch(ErrorHandler.action({message: "Batch Name error."}))
                      update = false 
                    }
                    /*if (StartDate !== "") {
                      var StartD = this.state.StartD.split(".")
                      if (StartD.length !== 3 || StartD[0].length !== 4 || isNaN(StartD[0]) || 
                                                (StartD[1].length <= 0 && StartD[1].length >= 3) || isNaN(StartD[1]) ||
                                                (StartD[2].length <= 0 && StartD[2].length >= 3)|| isNaN(StartD[2])) {
                        update = false 
                        this.props.setViewError("Startdate error")
                      }
                    }else {
                      update = false 
                      this.props.setViewError("Startdate error")
                    }*/
                    if (TargetWeight === "" || TargetWeight.length > 3 || isNaN(TargetWeight)) {
                      dispatch(ErrorHandler.action({message: "Tartget weight setting error"}))
                      update = false 
                    }
                    if (DevianceMin === "" || DevianceMin.length > 3 || isNaN(DevianceMin) || DevianceMin > 100){
                      dispatch(ErrorHandler.action({message: "DevianceMin setting error"}))
                      update = false 
                    }
                    if (DevianceMax === "" || DevianceMax.length > 3 || isNaN(DevianceMax) || DevianceMax > 100){
                      dispatch(ErrorHandler.action({message: "DevianceMax setting error"}))
                      update = false 
                    }
                    if (SelectedCurve === "") {
                      dispatch(ErrorHandler.action({message: "Growth curve setting error, please reselect a curve"}))
                      update = false 
                    }

                    var Pens = {}
                    if (selectedPens && selectedPens.length > 0) {
                      selectedPens.forEach(element => {
                        Pens[element.item] = true
                      });
                    }
                    if (Object.keys(Pens).length !== selectedPens.length) {
                      dispatch(ErrorHandler.action({message: "An error occurred during Pen assigning (please update the page)"}))
                      update = false
                    }

                    if (update) {
                      var newBatch = {gcurve:SelectedCurve, pens:Pens, time: StartDate.getTime(), datas:{target: TargetWeight, devianceMin: DevianceMin, devianceMax: DevianceMax}}
                      if (EditView === 1) {
                        dispatch(FirebaseBatch.create({FarmId:farmId, BatchId:SelectedBatch, Data: newBatch}))
                      } else if (EditView === 2) {
                        dispatch(FirebaseBatch.update({FarmId:farmId, BatchId:SelectedBatch, Data: newBatch}))
                      }
                      setEditView(0)
                    }
                  }
                }]} />
              </View>
            </View>
          )}
        </ScrollView>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Batchs
