import React from 'react'
import { View } from 'react-native'
import { Icon } from 'react-native-elements'
import { navigationRef } from '../Navigators/Root'
import { createStackNavigator } from '@react-navigation/stack'
import { createDrawerNavigator } from '@react-navigation/drawer';

import { IndexAdminContainer, SystemsBoard } from '../Containers'
import { SafeAreaView, StatusBar } from 'react-native'
import { useTranslation } from 'react-i18next'
import { useTheme } from '../Theme'

const AuthStack = createStackNavigator();

// @refresh reset
const AdminNavigator = () => {
  const { t } = useTranslation()
  const { Layout, darkMode, NavigationTheme } = useTheme()
  const { colors } = NavigationTheme
  return (
    <SafeAreaView style={[Layout.fill, { backgroundColor: colors.card }]}>
      <AuthStack.Navigator headerMode={'screen'} initialRouteName={"Home"} mode={"modal"}
        screenOptions={{
          title: 'AgrisysApp',
          headerTintColor: 'white',
          headerTitleAlign: 'center',
      }}>
        <AuthStack.Screen name="Home" component={IndexAdminContainer} 
          options={({ navigation }) => ({
            title: t('AgrisysAdmin.NavTitle'),
            headerLeft: () => (
              <View style={{padding:6, paddingLeft: 15}}>
                <Icon name="md-arrow-back" type='ionicon' size={25} color="white" iconStyle={{color:"white"}} onPress={() => navigationRef.current?.navigate('Main', {screen: 'Home'})}/>
              </View>
            ),
          })}
        />
        <AuthStack.Screen name="System" component={SystemsBoard} 
          options={({ navigation }) => ({
          })}
        />
      </AuthStack.Navigator>
    </SafeAreaView>
  )
}

export default AdminNavigator
