import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

async function fetch({FarmId, PenId, limitStart = 0, limitTo = 9999}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/datas/comments";
    if (_rend.getState().firebase.comments) {
      var time = await database().ref(ref + '/update_time').once('value').then((_timeData) => {
        if (_rend.getState().firebase.comments.update_time < _timeData.val()) return false
        else return true
      })
      if (time) {
        var StateComments = _rend.getState().firebase.comments
        if (!PenId && !StateComments[PenId]) {
          return StateComments
        }
      }
    }

    var res
    if (PenId) {
      ref += "/" + PenId
      await database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', snapshot => {
        var comment = {[snapshot.key]:{}}
        snapshot.forEach(c => {
          comment[snapshot.key][c.key] = c.val()
        }) 
        res = comment
      })
    } else {
      //TODO: ...Comment other than PenId
    }
    if (res) return res
  }
  return handleError({ message: 'Fecth Comments Error' })
}

async function create({FarmId, PenId, Action= null, Data= null, Date= null, Quantity= null, Weight= null, Code= null}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/datas/comments";
    if (PenId) {
      var ti = await database().ref(ref).update({
        update_time: database.ServerValue.TIMESTAMP
      })

      ref += "/" + PenId + "/"
      const commentRef = await database().ref(ref).push()
      const commentId = commentRef.key

      var res = await commentRef.set({
        time: Date,
        user: auth().currentUser.email,
        action: Action,
        quantity: Quantity,
        weight: Weight,
        code: Code,
        data: Data,
      }).then(async () => {
        var res2 = await fetch({FarmId:FarmId, PenId:PenId}, _rend).then((Comments)=>{
          return Comments;
        })
        return res2
      }).catch(error => {
        console.log("Create Comments Error: ", error);
        return handleError({ message: 'Create Comments Error' })
      })
      return res 
    } else {
      //TODO: ...Comment other than PenId
    }
  }
  return handleError({ message: 'Create Comments Error' })
}

export {
  fetch,
  create,
}